import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        parentName: sessionStorage.getItem('parentName') || "认证中心",
        childName: sessionStorage.getItem('childName') || "角色管理",
        menuList: [
            {
                path: "/Authentication",
                icon: "icon-authentication",
                link: "认证中心",
                array: [
                    {
                        link: "角色管理",
                        path: "/Authentication/Role"
                    },
                    {
                        link: "用户管理",
                        path: "/Authentication/User"
                    },
                    {
                        link: "用户反馈",
                        path: "/Authentication/Feedback"
                    },
                    {
                        link: "商家入驻",
                        path: "/Authentication/Enter"
                    },
                    {
                        link: "Token管理",
                        path: "/Authentication/Token"
                    }
                ]
            },
            {
                path: "/Operation",
                icon: "icon-operation",
                link: "运营中心",
                array: [
                    {
                        link: "商品上架",
                        path: "/Operation/newStock"
                    },
                    {
                        link: "商家列表",
                        path: "/Operation/shopList"
                    },
                    {
                        link: "订单列表",
                        path: "/Operation/orderList"
                    },
                    {
                        link: "首页配置",
                        path: "/Operation/homeConfig"
                    }
                ]
            },
            {
                path: "/Finance",
                icon: "icon-finance",
                link: "财务管理",
                array: [
                    {
                        link: "商户提现",
                        path: "/Finance/MerchantRec"
                    },
                    {
                        link: "退款申请",
                        path: "/Finance/RefundApl"
                    },
                    {
                        link: "平台对账",
                        path: "/Finance/PlatFormRec"
                    },
                    {
                        link: "用户提现",
                        path: "/Finance/UserWtd"
                    },
                    {
                        link: "支付类型",
                        path: "/Finance/PaymentTyp"
                    },
                    {
                        link: "发票审核",
                        path: "/Finance/AuditInv"
                    },
                    {
                        link: "商户明细",
                        path: "/Finance/ShopDetail"
                    }
                ]
            },
            {
                path: "/Monitoring",
                icon: "icon-monitoring",
                link: "运营监控",
                array: [
                    {
                        link: "运营监控1",
                        path: "/Monitoring/aaa"
                    },
                    {
                        link: "运营监控22",
                        path: "/Monitoring/bbb"
                    }
                ]
            }
        ]
    },
    getter: {
        
    },
    mutations: {
        setParentName(state, modify) {
            state.parentName = modify;
            sessionStorage.setItem('parentName', modify);
        },
        setChildName(state, modify) {
            state.childName = modify;
            sessionStorage.setItem('childName', modify);
        },
        resetState(state) {
            state.parentName = "认证中心";
            state.childName = "角色管理";
        }
    },
    action: {

    }
});

export default store;


