<template>
  <div class="c-box">
    <div class="refresh">
      <div class="refreshInner" @click="refresh">
        <span>数据概览</span>
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="c-merchant" v-loading="dataLoading">
      <!-- 支付金额数据 -->
      <ul>
        <li class="income-li" v-for="(item, index) in incomeList" :key="index">
          <p>{{item.title}}</p>
          <h3>
            <b>{{item.number}}（单）</b>
            <span>{{item.sum}}（元）</span>
          </h3>
        </li>
      </ul>
      <!-- 所有总数据 -->
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <!-- 表格option -->
      <div class="table-title">
        <div class="title-left">
          <el-button type="primary" icon="el-icon-upload2" style="margin-right:28px;" @click="exportExcel">导出</el-button>
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            :default-time="['00:00:00', '23:59:59']"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>审核状态</span>
            <el-select v-model="applyStatus" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" style="float:right;">
            <el-form-item>
              <el-input v-model="searchKey" @keyup.enter.native="search" placeholder="請输入商户名称进行搜索"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="shopLoading"
          :data="tableData"
          style="width: 100%;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID" width="70" fixed>
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column prop="shopName" label="商家名称" width="200"></el-table-column>
          <el-table-column prop="accountName" label="提现银行卡" width="220"></el-table-column>
          <el-table-column prop="bankCardName" label="所属银行" width="180"></el-table-column>
          <el-table-column prop="mobile" label="电话号码" width="160"></el-table-column>
          <el-table-column prop="createTime" label="发起提现时间" width="200" sortable></el-table-column>
          <el-table-column prop="accountCardholderName" label="持卡人" width=""></el-table-column>
          <el-table-column prop="cashAmount" label="提现金额" width="120" sortable></el-table-column>
          <el-table-column label="审批状态" width="120">
            <template slot-scope="scope">
              <div class="table-scope">    
                <template v-if="scope.row.applyStatus==2"> 
                  <span class="green">已通过</span>
                </template>   
                <template v-if="scope.row.applyStatus==3"> 
                  <span class="red">已驳回</span>
                </template>   
                <template v-if="scope.row.applyStatus==1"> 
                  <span class="grgy">待审批</span>
                </template>   
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="180" fixed="right">
            <template slot-scope="scope">
              <template v-if="scope.row.applyStatus==1">
                <el-button type="text" @click="passDialog(scope.row.cashId)">同意</el-button>
                <el-button type="text" @click="rejectDialog(scope.row.cashId)">驳回</el-button>
              </template>
              <template v-else>
                <el-button type="text" disabled>同意</el-button>
                <el-button type="text" disabled>驳回</el-button>
              </template>              
              <el-button type="text" @click="checkWithdraw(scope.row.cashId, scope.row.cashAmount, scope.row.beginTime, scope.row.endTime)">查看明细</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="shopLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
    <!--弹窗 商户提现明细-->
    <el-dialog title="商户提现明细" :visible.sync="dialogDetail" width="1056px">
      <div class="dialogDetail-title">
        总合计：
        <b>¥{{amount}}</b>元
      </div>
      <div class="dialog-table">
        <el-table v-loading="billLoading" :data="tableDetail" border style="width: 100%">
          <el-table-column prop="orderCode" label="订单号" width="200"></el-table-column>
          <el-table-column prop="userName" label="下单人"></el-table-column>
          <el-table-column prop="serviceAmount" label="单笔服务费" width="100"></el-table-column>
          <el-table-column prop="typeTxt" label="订单类型"></el-table-column>
          <el-table-column prop="billTxt" label="结算状态"></el-table-column>
          <el-table-column prop="realUnitPrice" label="支付金额"></el-table-column>
          <el-table-column prop="realShopUnitPirce" label="商家实际到账金额"></el-table-column>
          <el-table-column prop="createTime" label="核销时间" width="180"></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{dTotal}}条，共{{dPages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="detailPage"
              :current-page.sync="dPage"
              :page-count="dPages"
              layout="prev, pager, next, jumper"
              :total="dTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
      <div class="dialog-table">
        <el-table v-loading="changeLoading" :data="changeList" border style="width: 100%">
          <el-table-column prop="operateAmount" label="变更金额"></el-table-column>
          <el-table-column prop="remark" label="变更内容"></el-table-column>
          <el-table-column label="变更方式">
            <template slot-scope="scope">
              <a v-if="scope.row.operateType==1">支出</a>
              <a v-if="scope.row.operateType==2">收入</a>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="变更时间"></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{cTotal}}条，共{{cPages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="changePage"
              :current-page.sync="cPage"
              :page-count="cPages"
              layout="prev, pager, next, jumper"
              :total="cTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>
    <!--弹窗 确认通过审核-->
    <el-dialog title="提示" :visible.sync="dialogPass" width="540px">
      <div class="dialog-info">是否确认通过审核？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPass = false">取 消</el-button>
        <el-button type="primary" @click="pass">确 定</el-button>
      </span>
    </el-dialog>
    <!--弹窗 驳回-->
    <el-dialog title="驳回" :visible.sync="dialogFail" width="680px">
      <el-form ref="form" label-width="100px">
        <el-form-item label="驳回原因">
          <el-input type="textarea" v-model="textarea"></el-input>
        </el-form-item>
        <el-form-item label="上传图片">
          <el-upload
            ref="upload"
            :action="action"
            :headers="{Authorization: token}"
            list-type="picture-card"
            :on-preview="previewImg"
            :on-success="successUpload"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :modal="false" :visible.sync="isImgPreview">
            <img width="100%" :src="previewUrl" alt>
          </el-dialog>
        </el-form-item>
        <el-form-item label="发送短信">
          <el-radio v-model="isSend" label="1">是</el-radio>
          <el-radio v-model="isSend" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelBtn">取 消</el-button>
        <el-button type="primary" @click="reject">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      dialogDetail: false,   //查看明细弹窗
      dialogPass: false,     //通过弹窗
      dialogFail: false,     //驳回弹窗
      isImgPreview: false,
      previewUrl: "",        //图片预览url
      isSend: '1',              //radio选项
      textarea: '',             //驳回原因
      imgList: [],              //驳回图片
      startTime: '',    //开始时间
      endTime: '',      //结束时间
      singleStartTime: '',   //单个商家提现开始时间
      singleEndTime: '',     //单个商家提现开始时间
      page: 1,           //表格当前页数
      pages: 0,
      total: 0,
      dPage: 1,          //明细表格页数
      dPages: 0,
      dTotal: 0,
      rows: 10,
      cPage: 1,
      cPages: 0,
      cTotal: 0,
      cashId: '',         //商家id
      amount: 0,           //单个商家核销总金额
      applyStatus: '',  //审核状态id
      searchKey: '',
      dataLoading: true,
      shopLoading: true,
      billLoading: true,
      changeLoading: true,
      action: "/api/manage/img/upload",
      dateArray: "",
      token: '',
      options: [       //审核状态选项
        {
          id: '',
          value: "全部"
        },
        {
          id: 1,
          value: "待审批"
        },
        {
          id: 2,
          value: "已通过"
        },
        {
          id: 3,
          value: "已驳回"
        }
      ],
      incomeList: [],    //收入明细
      orderList: [],     //订单数据
      tableData: [],     //表格数据
      tableDetail: [],   //查看明细表格数据
      changeList: [],    //变更列表
    };
  },
  watch: {
    applyStatus(value) {     //选择审核状态
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
    }
  },
  methods: {
    getIncome() {    //总数据
      api.findShopAccountTotal({
        userType: 2,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    refresh() {      //数据刷新
      this.dataLoading = true;
      api.refreshShopAccountTotal({
        userType: 2,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    incomeCallback(res) {     //总数据封装回调
      if(res.code == 0) {
        let data = res.data, incomeList = [], orderList = [];
        incomeList = [
          {
              title: "微信支付收入笔数  |  收入总金额",
              number: data.wxPayTotal || 0,
              sum: data.wxPayMoney || 0
          }, {
              title: "余额支付收入笔数  |  收入总金额",
              number: data.yuePayTatal || 0,
              sum: data.yuePayMoney || 0
          }, {
              title: "微信支付退款笔数  |  退款总金额",
              number: data.wxRefundTatal || 0,
              sum: data.wxRefundTatalMoney || 0
          },
          {
              title: "余额支付退款笔数  |  退款总金额",
              number: data.yueRefundTatal || 0,
              sum: data.yueRefundTatalMoney || 0
          }
        ];
        orderList = [
          {
              iconImg: "icon-order1",
              number: data.toDayRefundTatal || 0,
              title: "今日退款订单总数(笔)"
          },
          {
              iconImg: "icon-money1",
              number: data.toDayRefundTatalMoney || 0,
              title: "今日退款订单总金额(元)"
          },
          {
              iconImg: "icon-order2",
              number: data.hisRefundTatal || 0,
              title: "历史退款订单总数(笔)"
          },
          {
              iconImg: "icon-money2",
              number: data.hisRefundTatalMoney || 0,
              title: "历史退款订单总金额(元)"
          }
        ];
        this.incomeList = incomeList;
        this.orderList = orderList;
        this.dataLoading = false;
      }
    },
    withdrawList() {      //提现列表
      // applyStatus   审核状态  1 待审批 2 审批通过 3 审批不通过
      api.searchShopWithdrawList({
        shopName: this.searchKey,
        applyStatus: this.applyStatus,
        startTime: this.startTime,
        endTime: this.endTime,
        page: this.page,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.pages = data.pages;
          this.total = data.total;
          this.tableData = data.list ? this.operateList(data.list) : [];
          this.shopLoading = false;
        } 
      }).catch(err => {})
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        e.createTime = e.createTime.replace('.0', '');
      });
      return arr;
    },
    //筛选时间
    pickTime(e) {
      if(e == null) {
        this.startTime = '';
        this.endTime = '';
      } else {
        this.startTime = e[0];
        this.endTime = e[1];
      }
      this.dataLoading = true;
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
      this.getIncome();
    },
    checkWithdraw(id, amount, startTime, endTime) {     //查看核销列表
      this.dialogDetail = true;
      this.cashId = id;
      this.amount = amount;
      this.singleStartTime = startTime;
      this.singleEndTime = endTime;
      this.billLoading = true;
      this.changeLoading = true;
      this.dPage = 1;
      this.cPage = 1;
      this.billList();
      this.changeListFunc();
    },
    billList() {     //核销列表
      api.findShopAccountDetailById({
        cashId: this.cashId,
        startTime: this.singleStartTime,
        endTime: this.singleEndTime,
        page: this.dPage,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.dPages = data.pages;
          this.dTotal = data.total;
          this.tableDetail = data.list ? this.flagType(data.list) : [];   //转换类型
          this.billLoading = false;
        }
      }).catch(err => {})
    },
    changeListFunc() {   //变更列表
      //operateType:变更类型 1 支出 2 收入 
      api.findAccountLog({
        cashId: this.cashId,
        startTime: this.singleStartTime,
        endTime: this.singleEndTime,
        page: this.cPage,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.cPages = data.pages;
          this.cTotal = data.total;
          this.changeList = data.list ? data.list : [];
          this.changeLoading = false;
        }
      }).catch(err => {});
    },
    search() {      //搜索
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
    },
    //清空
    empty() {
      this.shopLoading = true;
      this.searchKey = '';
      this.page = 1;
      this.withdrawList();
    },
    exportExcel() {    //导出报表
      location.href = '/api/manage/shopAccount/export?shopName='+ this.searchKey +'&applyStatus='+ this.applyStatus +'&startTime='+this.startTime+'&endTime='+this.endTime;
    },
    listPage(val) {    //商户列表修改分页
      this.shopLoading = true;
      this.page = val;
      this.withdrawList();
    },
    detailPage(val) {    //商户提现明细列表修改分页
      this.billLoading = true;
      this.dpage = val;
      this.billList();
    },
    changePage(val) {    //商户变更列表修改分页
      this.changeLoading = true;
      this.cpage = val;
      this.changeListFunc();
    },
    flagType(list) {    //判断订单类型
      // flagType:标记类型(1普通型  4砍价型  5秒杀型 6抽奖型 7团购型)'
      // isBill:'是否结清0未结清  1已结清 服务费 收商家钱'
      list.forEach((e, i) => {
        e.typeTxt = (type => {
          if(type == 1) return '普通型';
          if(type == 4) return '砍价型';
          if(type == 5) return '秒杀型';
          if(type == 6) return '抽奖型';
          if(type == 7) return '团购型';
        })(e.flagType);
        e.billTxt = (isBill => {
          if(isBill == 0) return '未结清';
          if(isBill == 1) return '已结清';
        })(e.isBill);
      })
      return list;
    },
    passDialog(id) {            //通过弹窗
      this.dialogPass = true;
      this.cashId = id;
    },
    pass() {                   //通过审核
      api.approvePass({
        id: this.cashId
      }).then(res => {
        if(res.code == 0) {
          this.$message.success('审核通过');
          this.dialogPass = false;
          this.withdrawList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {

      })
    },
    rejectDialog(id) {          //驳回弹窗
      this.dialogFail = true;
      this.cashId = id;
    },
    reject() {                //驳回审核
      if (!this.textarea) return this.$message.warning('请填写驳回原因');
      if (this.imgList.length <= 0) return this.$message.warning('请上传图片');
      let imgUrl = '';
      this.imgList.forEach((e, i) => { imgUrl += e + ','; });
      imgUrl = imgUrl.substring(0, imgUrl.length - 1);
      api.approveReject({
        cashId: this.cashId,
        content: this.textarea,
        imageUrl: imgUrl,
        isSend: this.isSend
      }).then(res => {
        if(res.code == 0) {
          this.$message.success('审核驳回');
          this.dialogFail = false;
          this.withdrawList();
          this.$refs.upload.clearFiles();
          this.imgList == [];
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {
        console.log(err)
      })
    },
    cancelBtn() {
      this.dialogFail = false;
    },
    previewImg(file) {
      this.previewUrl = file.url;
      this.isImgPreview = true;
    },
    successUpload(res, file, fileList) {
      let data = res.data;
      if(res.code == 0 && data.picUrl) this.imgList.push(data.picUrl);
    },
    handleRemove(file, fileList) {
      if(fileList.length > 0) this.imgList = [];
      fileList.forEach((e, i) => {
        if(e.response.data.picUrl) this.imgList.push(e.response.data.picUrl);
      })
    }
  },
  mounted() {
    this.getIncome();
    this.withdrawList();
    this.token = 'Bearer ' + localStorage.token;
  }
};
</script>

<style scoped lang="scss">
.c-merchant {
  height: 330px;
    ul {
    display: flex;
    li {
      flex: 1;
      height: 141px;
      background: #fff;
      border-radius: 6px;
      margin: 0 24px 24px 0;
    }
    li:last-child {
      margin-right: 0;
    }
    .income-li {
      > p {
        color: #666;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        text-align: center;
        margin-top: 42px;
      }
      > h3 {
        text-align: center;
        line-height: 26px;
        > b {
          color: #5769f0;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
        > span {
          color: #ffc000;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
      }
    }
    .order-li:nth-child(3),
    .order-li:nth-child(4) {
      .order-img {
        background: #eef2fc;
      }
    }
    .order-li {
      display: flex;
      align-items: center;
      .order-img {
        width: 66px;
        height: 66px;
        background: #fff2ca;
        border-radius: 100%;
        margin-left: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
          display: inline-block;
          background-image: url(../../../../assets/images/sprites-order.png);
          &.icon-order1 {
            width: 28px;
            height: 32px;
            background-position: -58px -66px;
          }
          &.icon-order2 {
            width: 28px;
            height: 32px;
            background-position: -10px -66px;
          }
          &.icon-money1 {
            width: 36px;
            height: 36px;
            background-position: -10px -10px;
          }
          &.icon-money2 {
            width: 36px;
            height: 36px;
            background-position: -66px -10px;
          }
        }
      }
      > h3 {
        flex: 1;
        margin-right: 29px;
        text-align: right;
        > b {
          font-size: 32px;
          color: #333;
          font-weight: bold;
          margin-top: 25px;
          line-height: 26px;
        }
        > p {
          font-size: 16px;
          color: #555;
          font-weight: 400;
          margin-top: 9px;
        }
      }
    }
  }
}
.dialogDetail-title {
  color: #555;
  font-size: 24px;
  font-weight: 500;
  line-height: 26px;
  margin-bottom: 24px;
  > b {
    color: #5769f0;
  }
}
</style>



