<template>
  <div class="c-box">
    <div class="refresh">
      <div class="refreshInner" @click="refresh">
        <span>数据概览</span>
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="c-merchant" v-loading="dataLoading">
      <ul>
        <li class="income-li" v-for="(item, index) in incomeList" :key="index">
          <p>{{item.title}}</p>
          <h3>
            <b>{{item.number}}（单）</b>
            <span>{{item.sum}}（元）</span>
          </h3>
        </li>
      </ul>
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button type="primary" icon="el-icon-upload2" style="margin-right:28px;" @click="exportExcel">导出</el-button>
        </div>
        <div class="title-right">
          <el-date-picker
            style="width:360px;"
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>订单类型</span>
            <el-select v-model="flagType" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <el-form :inline="true">
            <el-form-item>
              <el-input v-model="keywords" @keyup.enter.native="search" placeholder="請输入关键词进行搜索"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="tableLoading"
          :data="tableData"
          style="width: 100%;margin-top:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID" width="70">
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column prop="realUnitPrice" label="订单金额" sortable></el-table-column>
          <el-table-column prop="typeTxt" label="订单类型"></el-table-column>
          <el-table-column prop="mobile" label="下单电话" width="160"></el-table-column>
          <el-table-column prop="nickName" label="下单昵称"></el-table-column>
          <el-table-column prop="payTypeTxt" label="支付方式"></el-table-column>
          <el-table-column prop="createTime" label="下单时间" width="200" sortable></el-table-column>
          <el-table-column prop="payTime" label="使用时间" width="200" sortable></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="tableLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      beginTime: "",
      endTime: "",
      keywords: "",
      flagType: "",
      rows: 10,
      page: 1,
      pages: 1,
      total: 1,
      dataLoading: true,
      tableLoading: true,
      dateArray: "",
      tableData: [],
      incomeList: [],
      orderList: [],
      options: [
        {
          id: '',
          value: "全部"
        },
        {
          id: '1',
          value: "普通型"
        },
        {
          id: '4',
          value: "砍价型"
        },
        {
          id: '5',
          value: "秒杀型"
        },
        {
          id: '6',
          value: "抽奖型"
        },
        {
          id: '7',
          value: "团购型"
        }
      ]
    };
  },
  watch: {
    flagType(val) {
      this.tableLoading = true;
      this.page = 1;
      this.getList();
    }
  },
  methods: {
    getData() {    //总数据
      api.findShopAccountTotal({
        userType: '',
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    refresh() {      //数据刷新
      this.dataLoading = true;
      api.refreshShopAccountTotal({
        userType: '',
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    incomeCallback(res) {
      if(res.code == 0) {
        let data = res.data, incomeList = [], orderList = [];
        incomeList = [
          {
              title: "微信支付收入笔数  |  收入总金额",
              number: data.wxPayTotal || 0,
              sum: data.wxPayMoney || 0
          }, {
              title: "余额支付收入笔数  |  收入总金额",
              number: data.yuePayTatal || 0,
              sum: data.yuePayMoney || 0
          }, {
              title: "微信支付退款笔数  |  退款总金额",
              number: data.wxRefundTatal || 0,
              sum: data.wxRefundTatalMoney || 0
          },
          {
              title: "余额支付退款笔数  |  退款总金额",
              number: data.yueRefundTatal || 0,
              sum: data.yueRefundTatalMoney || 0
          }
        ];
        orderList = [
          {
              iconImg: "icon-order1",
              number: data.toDayRefundTatal || 0,
              title: "今日退款订单总数(笔)"
          },
          {
              iconImg: "icon-money1",
              number: data.toDayRefundTatalMoney || 0,
              title: "今日退款订单总金额(元)"
          },
          {
              iconImg: "icon-order2",
              number: data.hisRefundTatal || 0,
              title: "历史退款订单总数(笔)"
          },
          {
              iconImg: "icon-money2",
              number: data.hisRefundTatalMoney || 0,
              title: "历史退款订单总金额(元)"
          }
        ];
        this.incomeList = incomeList;
        this.orderList = orderList;
        this.dataLoading = false;
      }
    },
    getList() {
      api.PlatformList({
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.keywords,
        flagType: this.flagType,
        page: this.page,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.tableData = data.list ? this.flagTxt(data.list) : [];
          this.pages = data.pages;
          this.total = data.total;
          this.tableLoading = false;
        } 
      }).catch(err => {});
    },
    pickTime(e) {
      if(e == null) {
        this.beginTime = '';
        this.endTime = '';
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.dataLoading = true;
      this.tableLoading = true;
      this.page = 1;
      this.getData();
      this.getList();
    },
    exportExcel() {   //导出
      location.href = '/api/manage/Platform/export?beginTime='+this.beginTime+'&endTime='+this.endTime+'&keywords='+this.keywords+'&flagType='+this.flagType;
    },
    search() {      //搜索
      this.tableLoading = true;
      this.page = 1;
      this.getList();
    },
    //清空
    empty() {
      this.tableLoading = true;
      this.keywords = '';
      this.page = 1;
      this.getList();
    },
    listPage(val) {    //商户列表修改分页
      this.tableLoading = true;
      this.page = val;
      this.getList();
    },
    flagTxt(list) {    //判断订单类型
      // flagType:标记类型(1普通型  4砍价型  5秒杀型 6抽奖型 7团购型)'
      list.forEach((e, i) => {
          e.typeTxt = (type => {
              if(type == 1) return '普通型';
              if(type == 4) return '砍价型';
              if(type == 5) return '秒杀型';
              if(type == 6) return '抽奖型';
              if(type == 7) return '团购型';
          })(e.flagType);
          e.payTypeTxt = (type => {
              if(type == 0) return '免费赠送';
              if(type == 1) return '享七金币支付';
              if(type == 2) return '微信支付';
              if(type == 3) return '支付宝支付';
              if(type == 4) return '余额支付';
          })(e.payType);
          e.createTime = common.formatTime(new Date(e.createTime));
          e.payTime = common.formatTime(new Date(e.payTime));
      })
      return list;
    }
  },
  mounted() {
    this.getData();
    this.getList();
  }
};
</script>

<style scoped lang="scss">

.c-merchant {
  height: 330px;
  ul {
    display: flex;
    li {
      flex: 1;
      height: 141px;
      background: #fff;
      border-radius: 6px;
      margin: 0 24px 24px 0;
    }
    li:last-child {
      margin-right: 0;
    }
    .income-li {
      > p {
        color: #666;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        text-align: center;
        margin-top: 42px;
      }
      > h3 {
        text-align: center;
        line-height: 26px;
        > b {
          color: #5769f0;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
        > span {
          color: #ffc000;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
      }
    }
    .order-li:nth-child(3),
    .order-li:nth-child(4) {
      .order-img {
        background: #eef2fc;
      }
    }
    .order-li {
      display: flex;
      align-items: center;
      .order-img {
        width: 66px;
        height: 66px;
        background: #fff2ca;
        border-radius: 100%;
        margin-left: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
          display: inline-block;
          background-image: url(../../../../assets/images/sprites-order.png);
          &.icon-order1 {
            width: 28px;
            height: 32px;
            background-position: -58px -66px;
          }
          &.icon-order2 {
            width: 28px;
            height: 32px;
            background-position: -10px -66px;
          }
          &.icon-money1 {
            width: 36px;
            height: 36px;
            background-position: -10px -10px;
          }
          &.icon-money2 {
            width: 36px;
            height: 36px;
            background-position: -66px -10px;
          }
        }
      }
      > h3 {
        flex: 1;
        margin-right: 29px;
        text-align: right;
        > b {
          font-size: 32px;
          color: #333;
          font-weight: bold;
          margin-top: 25px;
          line-height: 26px;
        }
        > p {
          font-size: 16px;
          color: #555;
          font-weight: 400;
          margin-top: 9px;
        }
      }
    }
  }
}
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
}
</style>



