<template>
  <div class="c-box">
    <div class="refresh">
      <div class="refreshInner" @click="refresh">
        <span>数据概览</span>
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="c-merchant" v-loading="dataLoading"> 
      <ul>
        <li class="income-li" v-for="(item, index) in incomeList" :key="index">
          <p>{{item.title}}</p>
          <h3>
            <b>{{item.number}}（单）</b>
            <span>{{item.sum}}（元）</span>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">          
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>支付类型</span>
            <el-select v-model="payType" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" style="float:right;">
            <el-form-item>
              <el-input v-model="keywords" @keyup.enter.native="search" placeholder="請输入店铺名称或用户电话进行搜索" style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="tableLoading"
          :data="tableData"
          style="width: 100%; margin-top:20px"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID">
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column prop="realUnitPrice" label="支付金额" sortable :sort-method="sortByPrice"></el-table-column>
          <el-table-column prop="typeTxt" label="支付类型"></el-table-column>
          <el-table-column prop="mobile" label="用户电话"></el-table-column>
          <el-table-column prop="shopName" label="店铺名称"></el-table-column>
          <el-table-column prop="createTime" label="支付时间" sortable></el-table-column> 
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              :total="total"
              layout="prev, pager, next, jumper"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="tableLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      beginTime: "",
      endTime: "",
      payType: "",
      keywords: "",
      rows: 10,
      page: 1,
      pages: 1,
      total: 1,
      dataLoading: true,
      tableLoading: true,
      dateArray: "",
      tableData: [],
      incomeList: [],
      options: [
        {
          id: '',
          value: "全部"
        },
        {
          id: 0,
          value: "免费赠送"
        },
        {
          id: 1,
          value: "享七金币支付"
        },
        {
          id: 2,
          value: "微信支付"
        },
        {
          id: 3,
          value: "支付宝支付"
        },
        {
          id: 4,
          value: "余额支付"
        }
      ]
    };
  },
  watch: {
    payType(val) {    //状态筛选
      this.tableLoading = true;
      this.page = 1;
      this.paymentList();
    }
  },
  methods: {
    getData() {     //总数据
      api.findShopAccountTotal({
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    refresh() {      //数据刷新
      this.dataLoading = true;
      api.refreshShopAccountTotal({
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    incomeCallback(res) {
      if(res.code == 0) {
        let data = res.data, incomeList = [];
        incomeList = [
          {
              title: "微信支付收入笔数  |  收入总金额",
              number: data.wxPayTotal || 0,
              sum: data.wxPayMoney || 0
          }, {
              title: "余额支付收入笔数  |  收入总金额",
              number: data.yuePayTatal || 0,
              sum: data.yuePayMoney || 0
          }, {
              title: "微信支付退款笔数  |  退款总金额",
              number: data.wxRefundTatal || 0,
              sum: data.wxRefundTatalMoney || 0
          },
          {
              title: "余额支付退款笔数  |  退款总金额",
              number: data.yueRefundTatal || 0,
              sum: data.yueRefundTatalMoney || 0
          }
        ];
        this.incomeList = incomeList;
        this.dataLoading = false;
      }
    },
    paymentList() {    //列表
      api.paymentList({
        beginTime: this.beginTime,
        endTime: this.endTime,
        payType: this.payType,
        keywords: this.keywords,
        page: this.page,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.tableData = data.list ? this.flagType(data.list) : [];
          this.pages = data.pages;
          this.total = data.total;
          this.tableLoading = false;
        }
      }).catch(err => {});
    },
    pickTime(e) {
      if(e == null) {
        this.beginTime = '';
        this.endTime = '';
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.dataLoading = true;
      this.tableLoading = true;
      this.page = 1;
      this.getData();
      this.paymentList();

    },
    search() {      //搜索
      this.tableLoading = true;
      this.page = 1;
      this.paymentList();
    },
    // 清空
    empty(){
      this.keywords = "";
      this.tableLoading = true;
      this.page = 1;
      this.paymentList();
    },
    listPage(val) {
      this.tableLoading = true;
      this.page = val;
      this.paymentList();
    },
    flagType(list) {    //判断订单类型
      // flagType:标记类型(0免费赠送  1享七金币支付  2微信支付  3支付宝支付  4余额支付)'
      list.forEach((e, i) => {
        e.typeTxt = (type => {
            if(type == 0) return '免费赠送';
            if(type == 1) return '享七金币支付';
            if(type == 2) return '微信支付';
            if(type == 3) return '支付宝支付';
            if(type == 4) return '余额支付';
        })(e.payType);
        e.createTime = common.formatTime(new Date(e.createTime));
      })
      return list;
    },
    sortByPrice(obj1, obj2) {
      let price1 = obj1.realUnitPrice;
      let price2 = obj2.realUnitPrice;
      return price1 - price2;
    }
  },
  mounted() {
    this.getData();
    this.paymentList();
  }
};
</script>

<style scoped lang="scss">
.c-merchant {
  height: 165px;
  ul {
    display: flex;
    li {
      flex: 1;
      height: 141px;
      background: #fff;
      border-radius: 6px;
      margin: 0 24px 24px 0;
    }
    li:last-child {
      margin-right: 0;
    }
    .income-li {
      > p {
        color: #666;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        text-align: center;
        margin-top: 42px;
      }
      > h3 {
        text-align: center;
        line-height: 26px;
        > b {
          color: #5769f0;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
        > span {
          color: #ffc000;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
      }
    }
    .order-li:nth-child(3),
    .order-li:nth-child(4) {
      .order-img {
        background: #eef2fc;
      }
    }
    .order-li {
      display: flex;
      align-items: center;

      .order-img {
        width: 66px;
        height: 66px;
        background: #fff2ca;
        border-radius: 100%;
        margin-left: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
          display: inline-block;
          background-image: url(../../../../assets/images/sprites-order.png);
          &.icon-order1 {
            width: 28px;
            height: 32px;
            background-position: -58px -66px;
          }
          &.icon-order2 {
            width: 28px;
            height: 32px;
            background-position: -10px -66px;
          }
          &.icon-money1 {
            width: 36px;
            height: 36px;
            background-position: -10px -10px;
          }
          &.icon-money2 {
            width: 36px;
            height: 36px;
            background-position: -66px -10px;
          }
        }
      }
      > h3 {
        flex: 1;
        margin-right: 29px;
        text-align: right;
        > b {
          font-size: 32px;
          color: #333;
          font-weight: bold;
          margin-top: 25px;
          line-height: 26px;
        }
        > p {
          font-size: 16px;
          color: #555;
          font-weight: 400;
          margin-top: 9px;
        }
      }
    }
  }
}
</style>



