require("es6-promise").polyfill(); // 引入一次就行
import axios from "axios";
import router from "../src/router/index";

// 创建 axios 实例
// 这里 export  的原因是方便组件外使用 axios
export const Axios = axios.create({
  // baseURL: "https://www.xiang7.net/", //生产服
  // baseURL: "http://192.168.1.136/",      //测试服
  baseURL: "http://120.78.70.187:9081/",//测试服
  timeout: 15000
});

//POST传参序列化(添加请求拦截器)
// 在发送请求之前做某件事
axios.interceptors.request.use(
  config => {
    if (localStorage.token) {
      config.headers.Authorization = 'Bearer ' + localStorage.token;
    }
    return config;
  },
  error => {
    alert("错误的传参", "fail");
    return Promise.reject(error);
  }
);

//返回状态判断(添加响应拦截器)
axios.defaults.headers['Content-Type'] = 'application/json; charset=UTF-8';
axios.interceptors.response.use(
  res => {
    return res;
  },
  error => {
    if (error.response.status === 401 || error.response.status === 403) {
      router.push({
        path: "/login",
        query: { redirect: location.hostname }
      });
    } else if (error.response.status === 500) {
      // 服务器错误
      return Promise.reject(error.response.data);
    }
    // 返回 response 里的错误信息
    return Promise.reject(error.response.data);
  }
);

// 将 Axios 实例添加到Vue的原型对象上
export default {
  install(Vue) {
    Object.defineProperty(Vue.prototype, "$http", { value: Axios });
  }
};



