<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeModel"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>位置筛选</span>
            <el-cascader
              v-model="area"
              :options="options"
              :show-all-levels="false"
              :filterable="true"
              :clearable="true"
            ></el-cascader>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="输入商家名称进行搜索" @keyup.enter.native="shopList('reset', true)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="shopList('reset', true)">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <div class="operationBox">
          <el-button type="primary" plain @click="deleteBtn(ids)">批量删除</el-button>
          <div class="citySelect">
            <span>店铺总数：{{total}}</span>
            <el-select clearable v-model="city" placeholder="请选择" @change="shopList('reset', true)">
              <el-option
                v-for="item in cityOptions"
                :key="item.label"
                :label="item.label"
                :value="item.label"
              ></el-option>
            </el-select>
          </div>
        </div>
        <el-table
          :data="tableData" 
          style="width: 100%;marginTop:20px;" 
          @expand-change="dishList" 
          :row-key="getRowKeys"
          :expand-row-keys="expands"
          v-loading="loading"
          @selection-change="selectChange"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column type="expand" align="center">
            <template slot-scope="props">
              <table class="table-expand">
                <thead>
                  <tr>
                    <td width="200px">菜品名称</td>
                    <td width="200px">今日订单量</td>
                    <td width="200px">历史订单量</td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.row.dishList" :key="index">
                    <td class="expandSkuName">{{item.skuName}}</td>
                    <td>{{item.orderToday}}</td>
                    <td>{{item.orderHistory}}</td>
                  </tr>
                </tbody>
              </table>
            </template>
          </el-table-column>
          <el-table-column label="ID" prop="id" align="center" width="70">
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column label="店铺名称" prop="shopName" align="center"></el-table-column>
          <el-table-column label="入驻手机号" prop="mobile" align="center"></el-table-column>
          <el-table-column label="入驻时间" prop="settlingTime" align="center" sortable></el-table-column>
          <el-table-column label="详细地址" prop="address" align="center" width="300"></el-table-column>
          <el-table-column label="今日订单量" prop="orderToday" align="center" sortable :sort-method="sortByOrderT"></el-table-column>
          <el-table-column label="历史订单量" prop="orderHistory" align="center" sortable :sort-method="sortByOrderH"></el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="shopInfo(scope.$index)">编辑</el-button>
              <el-button type="text" size="small" @click="deleteBtn(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="changePage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!-- 菜品轮播图 -->
    <div class="dishBox">
      <div class="dishBox_tit">店铺所有商品</div>
      <div class="dishList" v-loading="dishLoading">
        <div class="dishList_inner">
          <div class="dishItem" v-for="(item, index) in carousel" :key="index">
            <div class="dishItem_cont">
              <img :src="item.skuPic ? item.skuPic : ''" alt>
              <div class="cont_info">
                <span>{{item.skuName}}</span>
                <span>已售{{item.orderHistory}}份</span>
              </div>
            </div>
            <div class="dishItem_oper">
              <div class="oper_edit" @click="goodsDetail(item.id, index)">编辑</div>
              <div class="oper_dele" @click="delGoodsDialog(item.id, index)">删除</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 删除商家弹窗 -->
    <el-dialog title="编辑商家信息" :visible.sync="deleteVisible" width="540px" v-loading="delLoading">
      <div class="dialog-info">是否删除该商家？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteShop">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑商家信息弹窗 -->
    <el-dialog title="编辑商家信息" :visible.sync="editVisible" width="50%" v-loading="shopInfoLoading">
      <div class="dialog_content">
        <el-form ref="form" label-width="150px" :model="shopObj">
          <el-form-item label="店铺名称">
            <el-input type="textarea" v-model="shopObj.shopName"></el-input>
          </el-form-item>
          <el-form-item label="入驻手机号">
            <el-input type="textarea" v-model="shopObj.mobile"></el-input>
          </el-form-item>
          <el-form-item label="所在位置">
            <el-input type="textarea" v-model="shopObj.address"></el-input>
          </el-form-item>
          <el-form-item label="管理员手机号">
            <el-input type="textarea" v-model="shopObj.adminMobile"></el-input>
          </el-form-item>
          <el-form-item label="核销员">
            <div class="cashierList">
              <div v-if="shopObj.shopCashier.length > 0" v-for="(item, index) in shopObj.shopCashier" :key="index" class="cashierItem">
                <div>{{item.cashierName}}</div>
                <i class="el-icon-error" @click="deleteCashierDialog(item.id)"></i>
              </div>
              <i class="addIcon el-icon-circle-plus" @click="addCashierVisible = true"></i>
            </div>
            <div></div>
          </el-form-item>
          <el-form-item label="店铺主图">
            <div v-if="shopObj.shopOwnerMap.length > 0">
              <div class="dia_item_Img" v-for="(item, index) in shopObj.shopOwnerMap" :key="index">
                <img :src="item.picUrl" alt>
                <i class="el-icon-error" @click="delGoodsImg(item)"></i>
              </div>
            </div>
            <el-upload
              v-if="shopObj.shopOwnerMap.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :on-success="successUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false;">取 消</el-button>
        <el-button type="primary" @click="updateShop">保 存</el-button>
      </span>
    </el-dialog>
    <!-- 添加核销员弹窗 -->
    <el-dialog title="添加核销员" :visible.sync="addCashierVisible" width="540px" v-loading="addCashierLoading">
      <el-form ref="form" label-width="150px">
        <el-form-item label="核销员手机号">
          <el-input type="textarea" v-model="addCachierTel"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCashierVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCashier">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除核销员弹窗 -->
    <el-dialog title="删除核销员" :visible.sync="delCashierVisible" width="540px" v-loading="delCashierLoading">
      <div class="dialog-info">是否删除该核销员？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delCashierVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteCashier">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑商品信息弹窗 -->
    <el-dialog title="编辑商品信息" :visible.sync="goodsVisible" width="50%" v-loading="goodsInfoLoading">
      <div class="dialog_content">
        <el-form ref="form" label-width="150px" :model="goodsObj">
          <el-form-item label="商品名称">
            <el-input type="textarea" v-model="goodsObj.skuName"></el-input>
          </el-form-item>
          <el-form-item label="商品价格">
            <el-input type="textarea" v-model="goodsObj.sellPrice"></el-input>
          </el-form-item>
          <el-form-item label="商品主图">
            <div class="shopMainImg">
              <img :src="goodsObj.skuPic" alt="">
              <div class="imgUpload">
                <el-upload
                  ref="upload"
                  :show-file-list="false"
                  :action="action"
                  :headers="{Authorization: token}"
                  list-type="picture-card"
                  :before-upload="mainImgBefore"
                  :on-success="mainImgUpload"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="商品轮播图">
            <div v-if="goodsObj.goodsSkuPicsList.length > 0">
              <div v-for="(item, index) in goodsObj.goodsSkuPicsList" :key="index" class="dia_item_Img">
                <img :src="item.picUrl" alt>
                <i class="el-icon-error" @click="delGoodsSkuPic(index, item.id)"></i>
              </div>
            </div>
            <el-upload
              v-if="goodsObj.goodsSkuPicsList.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :before-upload="mainImgBefore"
              :on-success="goodsImgUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="商品描述">
            <el-input type="textarea" v-model="goodsObj.contentText" style="width: 90%;" maxlength="200"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div>
              <div v-for="(item, index) in goodsObj.contentImgs" :key="index" class="dia_item_Img">
                <img :src="item" alt>
                <i class="el-icon-error" @click="delContentImgs(item)"></i>
              </div>
            </div>
            <el-upload
              v-if="goodsObj.contentImgs.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :before-upload="mainImgBefore"
              :on-success="contentPicUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="goodsVisible=false">取 消</el-button>
        <el-button type="primary" @click="updateGoods">保 存</el-button>
      </span>
    </el-dialog>
    <!-- 删除商品弹窗 -->
    <el-dialog title="删除商品" :visible.sync="delGoodsVisible" width="540px" v-loading="delGoodsLoading">
      <div class="dialog-info">是否删除该商品？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delGoodsVisible = false">取 消</el-button>
        <el-button type="primary" @click="delGoods">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import api from "../../../../assets/js/api";
import common from "../../../../assets/js/common.js";
export default {
  data() {
    return {
      expands: [],        //表格展开项的id
      loading: true,      //商家列表加载动画
      delLoading: false,  //删除商家加载动画
      dishLoading: true,  //菜品列表加载动画
      addCashierLoading: false,  //添加管理员加载动画
      delCashierLoading: false,  //删除管理员加载动画
      shopInfoLoading: false,    //商家详情加载动画
      goodsInfoLoading: false,   //菜品详情加载动画
      delGoodsLoading: false,    //删除菜品加载动画
      editVisible: false,    //商家详情弹窗
      deleteVisible: false,  //删除商家弹窗
      addCashierVisible: false,  //商家详情弹窗
      delCashierVisible: false,  //商家详情弹窗
      goodsVisible: false,       //商家详情弹窗
      delGoodsVisible: false,    //商家详情弹窗
      page: 1,
      rows: 10,
      pages: 1,
      total: 1,
      currentPage: 1,
      timeModel: "",         
      beginTime: "",         //开始时间
      endTime: "",           //结束时间
      city: "",              //城市
      position: "",          //位置
      id: "",                //商家单个删除id
      ids: "",               //商家批量删除id
      token: "",             //token
      action: "/api/manage/img/upload",        //上传图片地址
      addCachierTel: "",                       //
      shopId: "",                              //商家id
      shopIdx: 0,                              //商家序号
      cashierId: "",                           //核销员id
      goodsId: "",                             //商品id
      filters: {
        name: ""
      },
      shopObj: {
        id: "",
        shopName: "",
        mobile: "",
        address: "",
        adminMobile: "",
        shopCashier: [],
        shopOwnerMap: []
      },
      goodsObj: {
        id: "",
        skuName: "",
        sellPrice: "",
        skuPic: "",
        smallPicUrl: "",
        goodsSkuPicsList: [], //轮播图数组
        goodsSkuPics: "", //轮播图id
        spuId: "",
        content: "", //富文本文字加图片
        contentText: "", //富文本文字
        contentImgs: [] //富文本图片
      },
      area: [],
      options: [],
      cityOptions: [
        {
          value: "",
          label: "武汉"
        },
        {
          value: "",
          label: "十堰"
        }
      ],
      tableData: [],
      carousel: [],
      goodsContent: [],
      getRowKeys(row) {
        return row.id;
      }
    };
  },
  watch: {
    area(val) {      //位置筛选
      if (val.length == 2) {
        let idx1 = val[0],
          idx2 = val[1];
        this.position = this.options[idx1].children[idx2].label;
      } else {
        this.position = "";
      }
      this.loading = true;
      this.shopList("reset", true);
    }
  },
  methods: {
    areaOption() {     //下拉框专区
      api
        .areaList()
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.length > 0) {
            let obj = {};
            data && data.forEach((e, i) => {
              obj = {
                value: i,
                label: e.city,
                children: []
              };
              e.shopZoneItem && e.shopZoneItem.forEach((k, j) => {
                obj.children.push({
                  value: j,
                  label: k.name
                });
              });
              this.options.push(obj);
            });
          }
        })
        .catch(err => {});
    },
    shopList(types, dishflag) {    //商家列表
      if (types == "reset") {
        this.loading = true;
        this.page = 1;
      }
      api
        .shopList({
          beginTime: this.beginTime,
          endTime: this.endTime,
          position: this.position,
          keywords: common.blank(this.filters.name),
          city: common.isNull(this.city, "str"),
          page: this.page,
          rows: this.rows
        })
        .then(res => {
          let data = res.data;
          if (data) {
            this.pages = data.pages;
            this.total = data.total;
            this.tableData = data.list ? this.operateList(data.list) : [];
            if(this.tableData.length <= 0) this.carousel = [];
            if(dishflag) {
              this.shopId = this.tableData[0].id;
              this.shopIdx = 0;
              this.dishLoading = true;
              this.getDish(this.shopId, this.shopIdx);
            }
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    dishList(row, expandedRows) {    //展开菜品
      let id = row.id;
      this.tableData && this.tableData.forEach((e, i) => {
        if (e.id == id) {
          this.shopId = id;
          this.shopIdx = i;
          if (e.dishList) {
            this.carousel = e.dishList;
            return false;
          }
          this.dishLoading = true;
          this.getDish(this.shopId, this.shopIdx);
        }
      });
    },
    getDish(id, i) {    //调用菜品列表接口
      api
        .dishList({
          id: id
        })
        .then(res => {
          if (res.code == 0 && res.data.list) {
            let list = common.isNull(res.data.list, "arr");
            this.$set(this.tableData[i], "dishList", list);
            this.expands = [];
            this.expands.push(this.tableData[i].id);
            this.carousel = list;
          }
          this.dishLoading = false;
        })
        .catch(err => {
          this.dishLoading = false;
        });
    },
    deleteShop() {    //删除商家接口
      this.delLoading = true;
      api
        .deleteBusinesses({ ids: this.id })
        .then(res => {
          if (res.code == 0) {
            this.deleteVisible = false;
            this.loading = true;
            this.shopList('', true);
          } else {
            this.$message.error("删除失败");
          }
          this.delLoading = false;
        })
        .catch(err => {
          this.$message.error("删除失败");
          this.delLoading = false;
        });
    },
    delGoods() {      //删除商品接口
      this.delGoodsLoading = true;
      api
        .deleteGoods({ id: this.goodsId })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("删除成功");
            this.delGoodsVisible = false;
            this.getDish(this.shopId, this.shopIdx);
          }
          this.delGoodsLoading = false;
        })
        .catch(err => {
          this.delGoodsLoading = false;
        });
    },
    updateShop() {     //更新商家信息
      let shopObj = this.shopObj;
      if(!shopObj.shopName) return this.$message.warning("请输入店铺名称");
      if(!shopObj.mobile) return this.$message.warning("请输入入驻手机号");
      if(!shopObj.address) return this.$message.warning("请选择地址");
      // if(!shopObj.adminMobile) return this.$message.warning("请输入管理员手机号");
      if(shopObj.shopOwnerMap.length <= 0) return this.$message.warning("请上传店铺主图");

      this.shopInfoLoading = true;
      let params = {
        id: shopObj.id,
        address: shopObj.address,
        adminMobile: shopObj.adminMobile,
        mobile: shopObj.mobile,
        shopName: shopObj.shopName
      };
      api
        .updateShopInfo(params)
        .then(res => {
          if (res.code == 0) {
            this.shopList('', false);
            this.editVisible = false;
            this.$message.success(res.message);
          } else {
            this.$message.error("编辑失败");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
          this.$message.error("编辑失败");
        });
    },
    updateGoods() {     //更新商品信息
      let obj = this.goodsObj;
      if(!obj.skuName) return this.$message.warning('请输入商品名称');
      if(!obj.sellPrice) return this.$message.warning('请输入商品价格');
      if(!obj.skuPic) return this.$message.warning('请上传商品主图');
      if(!obj.contentText) return this.$message.warning('请输入商品描述');
      this.goodsInfoLoading = true;
      if(obj.contentText) obj.content = "<p>" + obj.contentText + "</p>";
      obj.contentImgs && obj.contentImgs.forEach((e, i) => {
        obj.content += '<img src="' + e + '" />';
      });
      api
        .updateGoods({
          id: obj.id,
          skuName: obj.skuName,
          sellPrice: obj.sellPrice,
          skuPic: obj.skuPic,
          smallPicUrl: obj.smallPicUrl,
          spuId: obj.spuId,
          content: obj.content,
          goodsSkuPics: obj.goodsSkuPics
        })
        .then(res => {
          if (res.code == 0) {
            this.getDish(this.shopId, this.shopIdx);
            this.goodsVisible = false;
          }
          this.goodsInfoLoading = false;
        })
        .catch(err => {
          this.goodsInfoLoading = false;
        });
    },
    addCashier() {     //添加核销员
      if (!this.addCachierTel) return this.$message.error("请填写手机号");
      this.addCashierLoading = true;
      api
        .updateClerk({
          type: 2,
          mobile: this.addCachierTel,
          shopId: this.shopObj.id
        })
        .then(res => {
          if (res.code == 0) {
            this.shopObj.shopCashier.push({id: res.data, cashierName: this.addCachierTel});
            this.addCachierTel = "";
            this.$message.success("添加成功");
            this.shopList('', false);
            this.addCashierVisible = false;
          } else {
            this.$message.error(res.message || "添加失败");
          }
          this.addCashierLoading = false;
        })
        .catch(err => {
          this.addCashierLoading = false;
        });
    },
    deleteCashier(index) {  //删除核销员
      this.delCashierLoading = true;
      api
        .updateClerk({
          type: 1,
          id: this.cashierId
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success(res.message);
            this.shopList('', false);
            this.delCashierVisible = false;
            this.shopObj.shopCashier && this.shopObj.shopCashier.forEach((e, i) => {
              if(e.id == this.cashierId) {
                this.shopObj.shopCashier.splice(i, 1);
              }
            })
          } else {
            this.$message.error(res.message || '删除失败');
          }
          this.delCashierLoading = false;
        })
        .catch(err => {
          this.delCashierLoading = false;
        });
    },
    delGoodsImg(data) {      //删除店铺主图
      this.shopInfoLoading = true;
      api
        .updateShopOwnerMap({
          type: 1,
          id: data.id
        })
        .then(res => {
          if(res.code == 0) {
            this.$message.success("删除成功");
            this.shopObj.shopOwnerMap && this.shopObj.shopOwnerMap.forEach((e, i) => {
              if (e.id == data.id) {
                this.shopObj.shopOwnerMap.splice(i, 1);
                return false;
              }
            });
          } else{
            this.$message.error("删除失败");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
        });
    },
    successUpload(res, file, fileList) {       //上传店铺主图
      let data = res.data;
      if (res.code == 0 && data.picUrl) this.addShopImg(data);
    },
    addShopImg(data) {     //把店铺主图存入数据库
      this.shopInfoLoading = true;
      api
        .updateShopOwnerMap({
          type: 2,
          id: this.shopObj.id,
          attachementId: data.id
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("新增成功");
            this.shopObj.shopOwnerMap.push({
              id: data.picUrl,
              picUrl: data.picUrl
            });
          } else {
            
            this.$message.error("新增失败");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
        });
    },
    mainImgBefore() {    //上传商品主图前加载动画
      this.goodsInfoLoading = true;
    },
    goodsImgUpload(res, file, fileList) {     //上传商品轮播图图片
      let data = res.data;
      if (res.code == 0 && data.picUrl) this.goodsObj.goodsSkuPicsList.push({
        id: data.id,
        picUrl: data.picUrl,
        smallPicUrl: data.smallPicUrl
      });
      this.goodsObj.goodsSkuPics += "," + data.id;
      this.goodsInfoLoading = false;
    },
    delGoodsSkuPic(i, id) {   //删除商品轮播图图片
      this.goodsObj.goodsSkuPicsList.splice(i, 1);
      let picsArr = this.goodsObj.goodsSkuPics.split(',');
      picsArr && picsArr.forEach((e, i) => {
        if(id == e) {
          picsArr.splice(i, 1);
        }
      })
      this.goodsObj.goodsSkuPics = picsArr.join(',');
    },
    mainImgUpload(res, file, fileList) {        //上传商品主图
      let data = res.data;
      this.goodsObj.skuPic = data.picUrl;
      this.goodsObj.smallPicUrl = data.smallPicUrl;
      this.goodsInfoLoading = false;
    },
    contentPicUpload(res, file, fileList) {    //上传商品描述富文本图片
      let data = res.data;
      if (res.code == 0 && data.picUrl)
        this.goodsObj.contentImgs.push(data.picUrl);
      this.goodsInfoLoading = false;
    },
    delContentImgs(item) {    //删除商品描述富文本图片
      this.goodsObj.contentImgs && this.goodsObj.contentImgs.forEach((e, i) => {
        if (e == item) {
          this.goodsObj.contentImgs.splice(i, 1);
          return;
        }
      });
    },
    shopInfo(i) {    //打开商家详情弹窗并获取信息
      this.shopIdx = i;
      for (let key in this.shopObj) {
        this.shopObj[key] = this.tableData[this.shopIdx][key];
      }
      this.editVisible = true;
    },
    goodsDetail(id, index) {     //打开商品详情弹窗并获取详情数据
      for (let key in this.goodsObj) {
        if (this.carousel[index][key])
          this.goodsObj[key] = this.carousel[index][key];
        if (key == "content" && this.goodsObj.content) {
          let obj = this.switchCont(this.goodsObj.content);
          this.goodsObj.contentText = obj.txt;
          this.goodsObj.contentImgs = obj.imgs;
        }
      }
      this.goodsVisible = true;
    },
    deleteBtn(id) {     //打开删除商家弹窗
      if (!id) return this.$message.warning("请先勾选商家");
      this.id = id;
      this.deleteVisible = true;
    },
    delGoodsDialog(id, index) {   //打开删除商品弹窗
      this.delGoodsVisible = true;
      this.goodsId = id;
    },
    deleteCashierDialog(id) {     //打开删除核销员弹窗
      this.delCashierVisible = true;
      this.cashierId = id;
    },
    selectChange(selection) {    //操作商家复选框
      let ids = "";
      selection && selection.forEach((e, i) => {
          ids += e.id + ",";
        });
      ids = ids.substring(0, ids.length - 1);
      this.ids = ids;
    },
    pickTime(e) {    //搜索框筛选时间
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.loading = true;
      this.shopList("reset", true);
    },
    empty() {     //搜索框清空
      this.loading = true;
      this.filters.name = "";
      this.shopList("reset", true);
    },
    changePage(val) {     //商家列表修改分页
      this.loading = true;
      this.page = val;
      this.shopList('', true);
    },
    sortByOrderT(obj1, obj2) {     //今日订单排序
      let str1 = obj1.orderToday;
      let str2 = obj2.orderToday;
      return str1 - str2;
    },
    sortByOrderH(obj1, obj2) {     //历史订单排序
      let str1 = obj1.orderHistory;
      let str2 = obj2.orderHistory;
      return str1 - str2;
    },
    operateList(arr) {      //修改时间格式
      arr && arr.forEach((e, i) => {
        e.settlingTime = e.settlingTime ? e.settlingTime.replace(".0", "") : '';
      });
      return arr;
    },
    switchCont(str) {         //转换富文本图片和文字
      var imgReg = /<img.*?(?:>|\/>)/gi,    //获取图片标签数组
        srcReg = /[img|IMG].*?src=['|"](.*?(?:[.gif|.jpg]))['|"].*?[/]?>/,    //获取图片属性数组
        txtReg = "(?<=>)(.|\n)*?(?=</p>)",        //获取p标签中的文字
        arr = [],
        imgs = [],
        txt = "";
      arr = str.match(imgReg);
      arr && arr.forEach((e, i) => {
        imgs.push(e.match(srcReg)[1]);
      });
      txt = str.match(txtReg)[0];
      return { txt, imgs };
    },
    /* 定位具体位置 */
    thisLocation() {
      this.map.plugin("AMap.Geolocation", () => {
        let geolocation = new AMap.Geolocation({
          // 是否使用高精度定位，默认：true
          enableHighAccuracy: true,
          // 设置定位超时时间，默认：无穷大
          timeout: 10000,
          // 定位按钮的停靠位置的偏移量，默认：Pixel(10, 20)
          buttonOffset: new AMap.Pixel(10, 20),
          //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
          zoomToAccuracy: false,
          //  定位按钮的排放位置,  RB表示右下
          buttonPosition: "RB"
        });
        this.map.addControl(geolocation);
        geolocation.getCurrentPosition();
        AMap.event.addListener(geolocation, "complete", data => {
          this.citycode = data.addressComponent.cityCode;
          this.thisPosition = data.position;
          this.thisPosition.message = data.formattedAddress;
          this.chosePosition = this.thisPosition;
          /* 画圆 */
          this.cancelLocation();
          this.addCircle();
          /* 拖拽选址 */
          this.positionPicker();
        });
        AMap.event.addListener(geolocation, "error", function(data) {
          alert("定位失败");
        });
      });
    },
    getMap() {
      this.events = {
        init: (o) => {
          let center = o.getCenter();
          this.center = [center.lng, center.lat];
          this.position = this.center;
          o.getCity(result => {
            this.address = result.province + result.city + result.district;
          })
        },
        click(eventName, handler, context) {
          this.position = [eventName.lnglat.lng, eventName.lnglat.lat];
          console.log(this.position)
        }
      };
    }
  },
  mounted() {
    this.areaOption();
    this.shopList('reset', true);
    this.token = "Bearer " + localStorage.token;
  }
};
</script>

<style scoped lang="scss">
.el-table .el-button {
  color: #5769f0;
  border: 0;
}
.selectBox {
  display: inline-block;
  margin-left: 20px;
  color: #666666;
}
.citySelect .el-select {
  width: 130px;
}
.operationBox {
  display: flex;
  justify-content: space-between;
}
.citySelect .el-select {
  margin-left: 20px;
}
.dishBox {
  background-color: #fff;
  margin-top: 20px;
  padding: 20px 30px;
  box-sizing: border-box;
  .dishBox_tit {
    color: #000000;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 30px;
  }
  .dishList {
    overflow: hidden;
    .dishList_inner {
      display: flex;
      overflow-x: auto;
      overflow-y: hidden;
      flex-wrap: nowrap;
      max-width: 10000px;
      .dishItem {
        width: 300px;
        margin-right: 10px;
        flex-shrink: 0;
        border-radius: 4px;
        overflow: hidden;
        .dishItem_cont {
          height: 200px;
          width: 100%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
          .cont_info {
            width: 100%;
            height: 40px;
            line-height: 40px;
            position: absolute;
            bottom: 0;
            left: 0;
            padding: 0 20px;
            display: flex;
            justify-content: space-between;
            color: #fff;
            background-color: #000000;
            opacity: 0.5;
          }
        }
        .dishItem_oper {
          height: 40px;
          line-height: 40px;
          width: 100%;
          display: flex;
          flex-direction: row;
          background-color: #f1f2f8;
          cursor: pointer;
          .oper_edit {
            flex: 1;
            text-align: center;
            border-right: 1px solid #fff;
            box-sizing: border-box;
          }
          .oper_dele {
            flex: 1;
            text-align: center;
            border-left: 1px solid #fff;
            box-sizing: border-box;
          }
        }
      }
    }
  }
}
.el-table__expanded-cell {
  padding-left: 60px !important;
  .el-form-item {
    display: inline-block;
    width: 200px;
    text-align: center;
    .el-form-item__label {
      width: 200px;
      text-align: center;
      padding: 0;
    }
    .el-form-item__content {
      clear: both;
      text-align: center;
    }
  }
}
.el-button--small {
  font-size: 14px;
}
.dialog_content {
  max-height: 600px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 50px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    background: rgba(0, 0, 0, 0.1);
  }
  .el-form-item {
    margin-top: 30px;
    font-size: 16px;
    &:first-child {
      margin-top: 0;
    }
    .el-textarea {
      width: 400px;
      height: 46px;
      textarea {
        box-sizing: border-box;
        height: 46px;
      }
    }
    .dia_item_Img {
      width: 170px;
      height: 170px;
      margin: 0 18px 18px 0;
      display: inline-block;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        &:last-child {
          margin-right: 0;
        }
      }
      i {
        position: absolute;
        top: -13px;
        right: -13px;
        font-size: 26px;
      }
    }
    .cashierList {
      display: flex;
      flex-wrap: wrap;
      .cashierItem {
        position: relative;
        padding: 0 20px;
        margin: 0 20px 20px 0;
        border-radius: 4px;
        border: 1px solid #e8e8e8;
        color: #666666;
        cursor: pointer;
        i {
          position: absolute;
          top: -13px;
          right: -13px;
          font-size: 26px;
        }
        &.active {
          border: 1px solid #5769f0;
          color: #5769f0;
        }
      }
      .addIcon {
        font-size: 40px;
        color: #5769f0;
        cursor: pointer;
      }
    }
  }
  .shopMainImg {
    width: 170px;
    height: 170px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .imgUpload {
      position: absolute;
      top: 10px;
      left: 10px;
      width: 100px;
      height: 100px;
      opacity: 0.6;
    }
  }
}
.table-expand {
  .expandSkuName {
    width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>


