<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeSection"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>审核状态</span>
            <el-select v-model="selectQuery" placeholder="请选择" @change="getTableData('reset')">
              <el-option
                v-for="item in optionQuery"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                v-model="filters.name"
                @keyup.enter.native="getTableData('reset')"
                placeholder="手机、用户昵称"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData('reset')">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain @click="batchAgree">批量同意</el-button>
        <el-button type="primary" plain @click="batchDisagree">批量驳回</el-button>
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%; margin-top:20px;"
          @selection-change="handleSelectionChange"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" :selectable="checkboxInit" fixed="left" width="45"></el-table-column>
          <el-table-column prop="cashId" label="ID" fixed width="70"></el-table-column>
          <el-table-column prop="userName" label="用户账号" width="140"></el-table-column>
          <el-table-column prop="nickName" label="用户昵称" width="120"></el-table-column>
          <el-table-column prop="mobile" label="电话号码" width="140"></el-table-column>
          <el-table-column label="支付类型" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payType=='微信'">
                  <i class="icon icon-weixin"></i>
                  <span>微信支付</span>
                </template>
                <template v-if="scope.row.payType=='银行卡'">
                  <i class="icon icon-yue"></i>
                  <span>银行卡</span>
                </template>
                <template v-if="scope.row.payType=='余额'">
                  <i class="icon icon-yue"></i>
                  <span>余额支付</span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="申请时间" width="200" sortable></el-table-column>
          <el-table-column prop="accountName" label="提现银行卡" width="180"></el-table-column>
          <el-table-column prop="accountCardholderName" label="持卡人" width="100"></el-table-column>
          <el-table-column prop="bankCardName" label="所属银行" width="140"></el-table-column>
          <el-table-column prop="cashAmount" label="提现金额" width="130" sortable></el-table-column>
          <el-table-column prop="serviceAmount" label="平台服务费" width="150" sortable></el-table-column>
          <el-table-column label="审核状态" width="100">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.applyStatus=='1'">
                  <span class="grgy">待审批</span>
                </template>
                <template v-if="scope.row.applyStatus=='2'">
                  <span class="green">已通过</span>
                </template>
                <template v-if="scope.row.applyStatus=='3'">
                  <span class="red">已驳回</span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="paidUserName" label="打款人" width="100" sortable></el-table-column>
          <el-table-column label="打款状态" width="100">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payStatus =='1'">
                  <span class="grgy">打款中</span>
                </template>
                <template v-if="scope.row.payStatus =='2'">
                  <span class="green">打款完成</span>
                </template>
                <template v-if="scope.row.payStatus =='3'">
                  <span class="red">打款失败</span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="100" fixed="right">
            <template slot-scope="scope">
              <span v-if="scope.row.applyStatus=='1'">
                <el-button type="text" @click="consent(scope.row)">同意</el-button>
                <el-button type="text" @click="reject(scope.row)">驳回</el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled>同意</el-button>
                <el-button type="text" disabled>驳回</el-button>
              </span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="loadingTable">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
    <!--弹窗 驳回-->
    <el-dialog title="驳回" :visible.sync="dialogFail" width="680px">
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="驳回原因">
          <el-select v-model="formReject.select" placeholder="请选择">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select==4">
          <el-input
            type="textarea"
            placeholder="请输入退款原因"
            v-model="formReject.textarea"
            maxlength="30"
            show-word-limit
            style="width:402px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="上传图片">
          <el-upload
            :on-exceed="limitError" 
            :limit="5"
            ref="rejectUpload"
            action="#"
            list-type="picture-card"
            :on-success="imgSuccess"
            :http-request="beforeImgUploadHome"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <!-- 弹窗 放大上传图片 -->
          <el-dialog :visible.sync="dialogVisible" :append-to-body="true">
            <img width="100%" :src="dialogImageUrl" alt>
          </el-dialog>
        </el-form-item>
        <el-form-item label="发送短信">
          <el-radio v-model="formReject.isSend" label="1">是</el-radio>
          <el-radio v-model="formReject.isSend" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFail = false">取 消</el-button>
        <el-button type="primary" @click="formRejectSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/assets/js/api.js";
import formatTime from "@/assets/js/common.js";
export default {
  data() {
    return {
      timeSection: "",
      filters: {
        name: ""
      },
      optionQuery: [
        {
          value: "",
          label: "全部状态"
        },
        {
          value: "1",
          label: "待审批"
        },
        {
          value: "2",
          label: "已通过"
        },
        {
          value: "3",
          label: "已驳回"
        }
      ],
      selectQuery: "",
      tableData: [],
      page: 1, //分页初始页
      pages: 0, //总页数
      total: 0, //总条数
      dialogImageUrl: "", //上传图片地址
      dialogImageArr: [],
      dialogVisible: false, //放大上传图片弹窗
      dialogFail: false, //驳回弹窗
      multipleSelection: [], //checkbox 存放
      startTime: "", //开始时间
      endTime: "", //结束时间
      //驳回原因
      formReject: {
        select: "",
        option: [
          {
            value: 1,
            label: "系统出错"
          },
          {
            value: 2,
            label: "恶意刷单"
          },
          {
            value: 3,
            label: "超过时间"
          },
          {
            value: 4,
            label: "其他原因"
          }
        ],
        textarea: "",
        isSend: "1"
      },
      cashId: "", //存放cashid
      loadingTable: false //数据加载效果
    };
  },
  mounted: function() {
    this.getTableData();
  },
  methods: {
    // 获取用户提现接口
    getTableData(types) {
      this.loadingTable = true;
      if (types == "reset") {
        this.page = 1;
      }
      api
        .searchUserAdvanceList({
          page: this.page,
          rows: this.rows,
          applyStatus: this.selectQuery,
          startTime: this.startTime,
          endTime: this.endTime,
          searchKey: formatTime.blank(this.filters.name)
        })
        .then(res => {
          let data = res.data;
          if (data) {
            for (let i = 0; i < data.list.length; i++) {
              data.list[i].createTime = formatTime.formatTime(
                new Date(data.list[i].createTime)
              );
              data.list[i].cashAmount = Number(data.list[i].cashAmount).toFixed(
                2
              );
              data.list[i].serviceAmount = Number(
                data.list[i].serviceAmount
              ).toFixed(2);
            }
            this.tableData = data.list ? data.list : [];
            this.total = data.total;
            this.pages = data.pages;
            this.loadingTable = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        e.createTime = e.createTime.replace('.0', '');
      });
      return arr;
    },
    //监听时间选择器
    pickTime(e) {
      if (e == null) {
        this.startTime = "";
        this.endTime = "";
      } else {
        this.startTime = e[0];
        this.endTime = e[1];
      }
      this.getTableData("reset");
    },
    //判断checkbox是否可勾选
    checkboxInit(row, index) {
      if (row.applyStatus == 1) {
        return 1; //可勾选
      } else {
        return 0; //不可勾选
      }
    },
    //监听checkbox
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //监听当前页变化
    handleCurrentChange(val) {
      this.page = val;
      this.getTableData();
    },
    //监听同意按钮
    consent(rowData) {
      this.$confirm("此操作将确定同意用户提现, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.postConsent(rowData);
          this.getTableData();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "操作失败"
          });
        });
    },
    // 获取同意接口
    postConsent(choseData) {
      let arr = [];
      if (choseData instanceof Array) {
        arr = choseData;
      } else {
        arr.push(choseData.cashId);
      }
      let cashId = "";
      if (arr.length > 1) {
        cashId = arr.join(",");
      } else {
        cashId = arr[0];
      }
      // let params = new URLSearchParams();
      // params.append("ids", cashId);
      let obj = {
        applyStatus: 2,
        ids: cashId
      };
      api
        .batchApplyPass(obj)
        .then(res => {
          if (res.message) {
            this.$message({
              type: "success",
              message: "操作成功!"
            });
          } else {
            this.$message.error("操作失败");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //批量同意弹窗
    batchAgree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("此操作无法撤回，将批量同意用户提现, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.cashId);
            });
            this.postConsent(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "操作失败"
            });
          });
      } else {
        this.$notify({
          title: "提示",
          message: "当前未选中任何用户，请至少选择一名用户!",
          type: "warning"
        });
      }
    },
    //清空
    empty() {
      this.filters.name = "";
      this.getTableData();
    },
    //监听驳回弹窗
    reject(row) {
      this.dialogFail = true;
      this.cashId = row.cashId;
      this.formReject.select = "";
      this.formReject.isSend = "1";
      this.formReject.textarea = "";
      this.dialogImageUrl = "";
      this.dialogImageArr = [], 
      this.$refs.rejectUpload.clearFiles();
    },
    //监听驳回弹窗确定按钮
    formRejectSubmit() {
      var arr = [];
      arr.push(this.cashId);
      this.postReject(arr);
    },
    //获取驳回接口
    postReject(arr) {
      if (!this.formReject.select)
        return this.$message.warning("请选择驳回原因");
      if (this.formReject.select == 4 && !this.formReject.textarea)
        return this.$message.warning("请填写驳回原因");
      let text = "";
      if(this.formReject.select != 4){
        text = this.formReject.option[this.formReject.select-1].label;
      }else{
        text = this.formReject.textarea;
      }  
      let urlArr = [];    
      this.dialogImageArr.forEach((value, i) => {
        urlArr.push(value.imgUrl);
      });
      this.dialogImageUrl = urlArr.join(",");  
      var param = {
        content: text,
        imageUrl: this.dialogImageUrl,
        isSend: this.formReject.isSend,
        items: arr
      }; 
      api
        .batchApplyReject(param)
        .then(res => {
          if (res.code == 0) {
            this.$message.success("审核驳回");
            this.dialogFail = false;
            this.getTableData();
          } else {
            this.$message.error(res.message);
          }
          this.dialogFail = false;
        })
        .catch(err => {
          this.dialogFail = false;
          console.log(err);
        });
    },
    //批量驳回弹窗
    batchDisagree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("此操作无法撤回，将批量驳回用户提现, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.cashId);
            });
            this.postReject(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "操作失败"
            });
          });
      } else {
        this.$notify({
          title: "提示",
          message: "当前未选中任何用户，请至少选择一名用户!",
          type: "warning"
        });
      }
    },
    //监听字数
    descInput() {
      this.formReject.remnant = this.formReject.textarea.length;
    },
    //上传数量限制
    limitError() {
      this.$message.error({
        message: "提示，最多上传五张图片。"
      });
    },
    //上传图片
    imgSuccess() {},
    beforeImgUploadHome(param) {
      let that = this;
      uploadImg(param.file).then(imgUrl => {
        that.dialogImageUrl = imgUrl;
        param.file.imgUrl = imgUrl;
        that.dialogImageArr.push(param.file);
      });
      return false;
    },
    handleRemove(file, fileList) {
      this.dialogImageArr = this.dialogImageArr.filter(ele => {
        ele.uid == file.uid;
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
  }
};

var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        console.log(err);
      });
  });
};
</script>

<style scoped lang="scss">
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
}

.form-remnant {
  position: absolute;
  top: 20px;
  color: #999;
  text-align: right;
  width: 392px;
}
</style>



