<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeDate"
            unlink-panels
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="pickTime"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
          ></el-date-picker>
          <div class="selectBox">
            <span>条件区分</span>
            <el-select
              v-model="customerTypeValue"
              @change="changeSearch"
              clearable
              placeholder="请选择"
            >
              <el-option
                v-for="item in customerTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="selectBox">
            <span>订单类型</span>
            <el-select
              v-model="flagType"
              @change="changeSearch"
              clearable
              placeholder="请选择"
            >
              <el-option
                v-for="item in orderTypeoptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" v-model="filters">
            <el-form-item>
              <el-input
                class="searchInput"
                v-model="orderName"
                placeholder="输入商家店铺名称或者订单编号进行搜索"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="searchList">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <div class="operationBox">
          <div class="citySelect">
            <span>订单状态</span>
            <el-select
              v-model="orderStatusValue"
              clearable
              @change="changeSearch"
              placeholder="请选择"
            >
              <el-option
                v-for="item in orderStatusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{ prop: 'date', order: 'descending' }"
        >
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-table
                  :data="props.row.orderItems"
                  style="width: 100%;marginTop:20px;"
                  class="detailTable"
                >
                  <el-table-column
                    prop="goodsPrice"
                    label="商品金额"
                    align="center"
                  ></el-table-column>
                  <el-table-column
                    prop="goodsSkuName"
                    label="商品名称"
                    align="center"
                  ></el-table-column>
                  <el-table-column
                    prop="usageState"
                    label="使用状态"
                    align="center"
                    :formatter="usageStateFilter"
                  ></el-table-column>
                  <el-table-column
                    :formatter="formatTimeDate"
                    prop="verificationTime"
                    label="核销时间"
                    align="center"
                  ></el-table-column>
                  <el-table-column label="操作" align="center">
                    <template slot-scope="props">
                      <el-button
                        type="text"
                        size="small"
                        @click="openDetailVisible(props.row)"
                        :disabled="props.row.usageState == '1'"
                      >
                        修改
                      </el-button>
                    </template></el-table-column
                  >
                </el-table>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="orderCode"
            label="订单编号"
            width="150"
            align="center"
            :sortable="true"
          ></el-table-column>
          <el-table-column
            prop="mobile"
            label="电话号码"
            width="120"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="payType"
            label="支付类型"
            width="120"
            :formatter="formatpayType"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="skuAmount"
            label="订单金额"
            width="120"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="customerType"
            label="订单类型"
            :formatter="formatOrderTypeoptions"
            width="120"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="flagType"
            label="订单来源"
            width="120"
            :formatter="formatCustomerType"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="userName"
            label="下单人"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="shopName"
            label="商家店铺"
            width="170"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="orderType"
            label="订单状态"
            width="160"
            :formatter="formatOrderType"
            align="center"
          ></el-table-column>
          <el-table-column
            :sortable="true"
            prop="createTime"
            :formatter="formatTimeDate"
            label="下单时间"
            width="160"
            align="center"
          ></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">
            共<span
              v-loading="totalFlag"
              element-loading-spinner="el-icon-loading"
              >{{ total }}</span
            >条，共<span
              v-loading="totalFlag"
              element-loading-spinner="el-icon-loading"
              >{{ pages }}</span
            >页
          </div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
            <!-- 修改订单弹窗 -->
            <el-dialog
              title="订单状态修改"
              :visible.sync="detailVisible"
              width="600px"
            >
              <el-form ref="formEditor" label-width="100px">
                <el-form-item label="订单状态">
                  <el-input value="已核销" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="处理人">
                  <el-input :value="uesrName" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="修改原因">
                  <el-input
                    type="textarea"
                    placeholder="请输入修改原因"
                    v-model="formEditorText"
                    maxlength="30"
                    :rows="3"
                    style="width:402px;"
                    show-word-limit
                  ></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="detailVisible = false">取 消</el-button>
                <el-button
                  type="primary"
                  @click="editorStatue"
                  :loading="editorSure"
                  >确 定</el-button
                >
              </div>
            </el-dialog>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../../../../assets/js/api";
import common from "../../../../assets/js/common";
export default {
  data() {
    return {
      loadingTable: false, //table表格数据加载动画
      timeDate: null,
      timeStart: "",
      timeEnd: "",
      filters: "",
      orderName: null,
      uesrName: localStorage.getItem("ms_username"),
      currentPage: 1,
      orderStatusValue: "",
      flagType: "",
      orderTypeoptions: [
        {
          value: "1",
          label: "普通型"
        },
        {
          value: "4",
          label: "砍价拼团"
        },
        {
          value: "5",
          label: "限量秒杀"
        },
        {
          value: "6",
          label: "抽奖型"
        },
        {
          value: "7",
          label: "团购型"
        }
      ],
      useageOpt: [{ label: "否", value: "0" }, { label: "是", value: "1" }],
      orderStatusOptions: [
        {
          value: "1",
          label: "待付款"
        },
        {
          value: "2",
          label: "待收货"
        },
        {
          value: "3",
          label: "已完成"
        },
        {
          value: "4",
          label: "退款申请中"
        },
        {
          value: "5",
          label: "已退款"
        },
        {
          value: "10",
          label: "取消"
        }
      ],
      detailVisible: false,
      tableData: null,
      total: 0,
      pageSize: 10,
      customerTypeValue: null,
      customerTypeOptions: [
        {
          label: "平台订单",
          value: 1
        },
        {
          label: "商家订单",
          value: 2
        }
      ],
      orderId: null,
      formEditorText: "已使用，但未核销", //修改原因
      payStatus: [
        {
          label: "免费赠送",
          value: "0"
        },
        {
          label: "享七金币支付",
          value: "1"
        },
        {
          label: "微信支付",
          value: "2"
        },
        {
          label: "支付宝支付",
          value: "3"
        },
        {
          label: "余额支付",
          value: "4"
        }
      ],
      FId: null, //一级表的id
      Fshopid: null, //一级表的shopid
      orderId: null, //二级表的id
      editorSure: false,
      totalFlag: false
    };
  },
  computed: {
    pages() {
      return Math.ceil(this.total / 10);
    }
  },
  methods: {
    formatOrderTypeoptions(row, column, cellValue, index) {
      return this.orderTypeoptions.map((ele, key) => {
        if (ele.value == cellValue) {
          return ele.label;
        }
      });
    },
    formatCustomerType(row, column, cellValue, index) {
      return this.customerTypeOptions.map((ele, key) => {
        if (ele.value == cellValue) {
          return ele.label;
        }
      });
    },
    openDetailVisible(item) {
      //打开弹窗
      this.detailVisible = true;
      this.FId = item.Fid;
      this.orderId = item.id;
      this.Fshopid = item.Fshopid;
    },
    formatOrderType(row, column, cellValue, index) {
      return this.orderStatusOptions.map((ele, index) => {
        if (ele.value == cellValue) {
          return ele.label;
        }
      });
    },
    searchList() {
      //搜索列表
      this.currentPage = 1;
      this.getlist();
      this.getTotal();
    },
    //清空
    empty() {
      this.orderName = "";
      this.getlist();
      this.getTotal();
    },
    editorStatue() {
      //修改订单状态
      this.currentPage = 1;
      this.editorSure = true;
      api
        .orderEditor({
          remark: this.formEditorText,
          orderId: this.orderId,
          id: this.FId,
          shopId: this.Fshopid
        })
        .then(res => {
          if (res.code == 0) {
            this.detailVisible = false;
            this.formEditorText = "已使用，但未核销";
            this.editorSure = false;
            this.getlist();
            this.getTotal();
          }
        })
        .catch(err => {
          this.$confirm("接口报错" + err.message)
            .then(_ => {
              this.detailVisible = false;
              this.formEditorText = "已使用，但未核销";
              this.editorSure = false;
              this.getlist();
              this.getTotal();
            })
            .catch(_ => {});
        });
    },
    usageStateFilter(row, column, cellValue, index) {
      return cellValue == 1 ? "已使用" : "未使用";
    },
    formatpayType(row, column, cellValue, index) {
      return this.payStatus.map((ele, index) => {
        if (ele.value == cellValue) {
          return ele.label;
        }
      });
    },
    formatTimeDate(row, column, cellValue, index) {
      return common.formatTime(new Date(cellValue));
    },
    pickTime(e) {
      //选中时间
      if (e == null) {
        this.timeStart = "";
        this.timeEnd = "";
      } else {
        this.timeStart = e[0];
        this.endTime = e[1];
      }
      this.getlist();
      this.getTotal();
    },
    changeSearch() {
      this.currentPage = 1;
      this.getlist();
      this.getTotal();
    },
    handleClick(row) {
      // console.log(row);
    },
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.getlist();
      this.getTotal();
    },
    handleClose(done) {
      // this.$confirm("确认关闭？")
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    },
    getlist() {
      this.loadingTable = true;
      api
        .orderList({
          beginTime: this.timeStart,
          sourceType: this.customerTypeValue,
          endTime: this.endTime,
          keywords: this.orderName,
          orderStatus: this.orderStatusValue,
          orderType: this.flagType,
          page: this.currentPage,
          rows: 10
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.list) {
            this.loadingTable = false;
            this.tableData = JSON.parse(JSON.stringify(data.list));
            this.tableData.map((ele, index) => {
              let Fid = ele.id;
              let FshopID = ele.shopId;
              return ele.orderItems.map((ele, index) => {
                ele.Fid = Fid;
                ele.Fshopid = FshopID;
                return ele;
              });
            });
          }
        })
        .catch(err => {
          this.$confirm("接口报错" + err.message)
            .then(_ => {
              this.loadingTable = false;
            })
            .catch(_ => {});
        });
    },
    getTotal() {
      this.totalFlag = true;
      api
        .orderTotal({
          beginTime: this.timeStart,
          sourceType: this.customerTypeValue,
          endTime: this.endTime,
          keywords: this.orderName,
          orderStatus: this.orderStatusValue,
          orderType: this.flagType,
          page: 1,
          rows: 10
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0) {
            this.total = data;
            this.totalFlag = false;
          }
        })
        .catch(err => {
          this.totalFlag = false;
          this.$confirm("接口报错" + err.message)
            .then(_ => {})
            .catch(_ => {});
        });
    }
  },
  mounted() {
    this.getlist();
    this.getTotal();
  }
};
</script>

<style scope="scoped" lang="scss">
.selectBox {
  display: inline-block;
  margin-left: 20px;
  color: #666666;
}
.searchInput {
  input {
    width: 300px;
  }
}
.operationBox {
  .citySelect {
    text-align: right;
    .el-select {
      width: 140px;
    }
  }
}
.detailTable {
  th {
    background-color: #fff !important;
  }
}
</style>



