<template>
  <div class="c-tag">当前位置：
    <span>{{this.$store.state.parentName}}</span>
    <span v-if="this.$store.state.childName">-</span>  
    <span v-if="this.$store.state.childName">{{this.$store.state.childName}}</span>
  </div>
</template>

<script>
export default {
  name: "Tag",
  data() {
    return {};
  },
  methods: {
    
  }
};
</script>

<style scoped lang="scss">
.c-tag {
  width: 100%;
  height: 50px;
  line-height: 50px;
  color: #555;
  font-size: 14px;
  text-indent: 29px;
  font-size: 14px;
  background: #fff;
  span {
    color: #555;
    text-decoration: none;
    // &:hover {
    //   color: #5769f0;
    // }
  }
}
</style>


