import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/page/Login.vue'
import Home from '@/components/common/Home.vue'
//认证中心
import Authentication from '@/components/page/authentication/Authentication.vue'  
import Role from '@/components/page/authentication/role/Role.vue' 
import User from '@/components/page/authentication/user/User.vue' 
import Feedback from '@/components/page/authentication/feedback/Feedback.vue'
import Enter from '@/components/page/authentication/enter/Enter.vue' 
import Token from '@/components/page/authentication/token/Token.vue' 
//运营中心
import Operation from '@/components/page/operation/operation.vue' 
import NewStock from '@/components/page/operation/newStock/newStock.vue' 
import ShopList from '@/components/page/operation/shopList/shopList.vue'
import OrderList from '@/components/page/operation/orderList/orderList.vue'
import HomeConfig from '@/components/page/operation/homeConfig/homeConfig.vue'

//财务管理
import Finance from '@/components/page/finance/Finance.vue' 
import MerchantRec from '@/components/page/finance/merchantRec/MerchantRec.vue'
import RefundApl from '@/components/page/finance/refundApl/RefundApl.vue'
import PlatFormRec from '@/components/page/finance/platFormRec/PlatFormRec.vue'
import UserWtd from '@/components/page/finance/userWtd/UserWtd.vue'
import PaymentTyp from '@/components/page/finance/paymentTyp/PaymentTyp.vue'
import AuditInv from '@/components/page/finance/auditInv/AuditInv.vue'
import ShopDetail from '@/components/page/finance/ShopDetail/ShopDetail'

//运营监控
import Monitoring from '@/components/page/monitoring/Monitoring.vue' 


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', 
      component: Home,
      redirect:'/Authentication/Role',
      children: [
        {
          //认证中心
          path: '/Authentication', 
          component: Authentication,
          redirect:'/Authentication/Role',
          children: [
            {
              path: '/Authentication/Role', 
              component: Role
            },
            {
              path: '/Authentication/User', 
              component: User
            },
            {
              path: '/Authentication/Feedback', 
              component: Feedback
            },
            {
              path: '/Authentication/Enter', 
              component: Enter
            },
            {
              path: '/Authentication/Token', 
              component: Token
            }            
          ]
        },
        {
          //运营中心
          path: '/Operation', 
          component: Operation,
          redirect:'/Operation/newStock',
          children: [  
            {
              path: '/Operation/newStock', 
              component: NewStock
            },
            {
              path: '/Operation/shopList', 
              component: ShopList
            },
            {
              path: '/Operation/orderList',
              component: OrderList
            },
            {
              path: '/Operation/homeConfig',
              component: HomeConfig
            }       
          ]
        },
        {
          //财务管理
          path: '/Finance', 
          component: Finance,
          redirect:'/Finance/MerchantRec',
          children: [
            {
              path: '/Finance/MerchantRec', 
              component: MerchantRec
            },
            {
              path: '/Finance/RefundApl', 
              component: RefundApl
            },
            {
              path: '/Finance/PlatFormRec', 
              component: PlatFormRec
            },
            {
              path: '/Finance/UserWtd', 
              component: UserWtd
            }, 
            {
              path: '/Finance/PaymentTyp', 
              component: PaymentTyp
            },
            {
              path: '/Finance/AuditInv', 
              component: AuditInv
            },
            {
              path: '/Finance/ShopDetail',
              component: ShopDetail
            }
          ]
        },
        {
          //运营中心
          path: '/Monitoring', 
          component: Monitoring,
          // redirect:'/operation/Operation',
          children: [              
          ]
        }
      ]
    },
    {
      path: '/Login', 
      component: Login
    }
  ]
})



