<template>
  <div class="c-box">
    <div class="c-table">
      <div class="c-city">
        <a
          :class="{acitve:index==cityIndex}"
          v-for="(item, index) in citys"
          :key="index"
          @click="citySwitch(index)"
        >{{item.city}}</a>
      </div>
      <div class="c-cityBox" v-loading="loadingList">
        <!-- 首页banner -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">首页banner</span>
            <el-upload
              action="#"
              :show-file-list="false"
              :on-success="imgSuccess"
              :before-upload="beforeImgUploadHome"
            >
              <el-button type="primary" icon="el-icon-plus" :loading="loadingBtn">添加</el-button>
            </el-upload>
            <el-button
              class="config-title-btn"
              type="success"
              @click="bannerSave(1+confinIndex,'bannerHome')"
            >保存</el-button>
          </div>
          <div class="config-box banner-box">
            <div class="banner-item" v-for="(item, index) in bannerHome" :key="index">
              <img class="banner-img" :src="item.imgUrl" alt>
              <a class="banner-del" @click="deleteItem(index, 'bannerHome')">
                <i class="el-icon-delete"></i>
              </a>
              <el-input
                class="banner-link"
                type="textarea"
                placeholder="请输入内容"
                v-model="item.linkUrl"
              ></el-input>
              <div class="banner-switchLink">
                <span>是否跳转</span>
                <el-switch v-model="item.isJump"></el-switch>
              </div>
              <el-input
                class="banner-title"
                type="textarea"
                placeholder="这里是无跳转时可显示的文字提示..."
                v-model="item.remark"
              ></el-input>
              <div class="banner-rank">
                <span>顺序调整</span>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back"
                  @click="sortleftGoods(index,'bannerHome')"
                ></el-button>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back deg180"
                  @click="sortrightGoods(index,'bannerHome')"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 钜惠推荐 -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">钜惠推荐</span>
            <el-button
              class="config-title-btn"
              type="primary"
              icon="el-icon-plus"
              @click="addGoods(41,'goodsRecommend')"
            >添加</el-button>
            <el-button
              class="config-title-btn"
              type="success"
              @click="savecommend(2+confinIndex,'goodsRecommend')"
            >保存</el-button>
          </div>
          <div class="config-box recommend-box">
            <div class="goods-item" v-for="(item,index) in goodsRecommend" :key="index">
              <div class="goods-img">
                <img
                  :src="item.goodsSkuOut[0].skuPic?item.goodsSkuOut[0].skuPic:item.goodsSku.skuPic"
                  alt
                >
                <div class="goods-img-text">
                  <span>剩余{{item.stockNum}}份</span>
                  <i class="el-icon-delete" @click="deleteItem(index, 'goodsRecommend')"></i>
                </div>
              </div>
              <div class="goods-title">
                <p>{{item.goodsSkuOut[0].skuName?item.goodsSkuOut[0].skuName:item.goodsSku.skuName}}</p>
              </div>
              <div class="goods-price">
                <b>¥{{item.goodsPromotionRules.actAmount}}</b>
                <span>¥{{item.goodsSkuOut[0].sellPrice?item.goodsSkuOut[0].sellPrice:item.goodsSku.sellPrice}}</span>
              </div>
              <div class="goods-rank">
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back"
                  @click="sortleftGoods(index,'goodsRecommend')"
                ></el-button>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back deg180"
                  @click="sortrightGoods(index,'goodsRecommend')"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 1元秒杀 -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">1元秒杀</span>
            <el-button
              class="config-title-btn"
              type="primary"
              icon="el-icon-plus"
              @click="addGoods(44,'goodSeckill')"
            >添加</el-button>
            <el-button
              class="config-title-btn"
              @click="savecommend(3+confinIndex,'goodSeckill')"
              type="success"
            >保存</el-button>
          </div>
          <div class="config-box recommend-box">
            <div class="goods-item" v-for="(item,index) in goodSeckill" :key="index">
              <div class="goods-img">
                <img
                  :src="item.goodsSkuOut[0].skuPic?item.goodsSkuOut[0].skuPic:item.goodsSku.skuPic"
                  alt
                >
                <div class="goods-img-text">
                  <span>剩余{{item.stockNum}}份</span>
                  <i class="el-icon-delete" @click="deleteItem(index, 'goodSeckill')"></i>
                </div>
              </div>
              <div class="goods-title">
                <p>{{item.goodsSkuOut[0].skuName?item.goodsSkuOut[0].skuName:item.goodsSku.skuName}}</p>
              </div>
              <div class="goods-price">
                <b>¥{{item.goodsPromotionRules.actAmount}}</b>
                <span>¥{{item.goodsSkuOut[0].sellPrice?item.goodsSkuOut[0].sellPrice:item.goodsSku.sellPrice}}</span>
              </div>
              <div class="goods-rank">
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back"
                  @click="sortleftGoods(index,'goodSeckill')"
                ></el-button>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back deg180"
                  @click="sortrightGoods(index,'goodSeckill')"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 精选商家 -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">精选商家</span>
            <el-button
              class="config-title-btn"
              type="primary"
              icon="el-icon-plus"
              @click="showShop"
            >添加</el-button>
            <el-button class="config-title-btn" @click="saveShop(4+confinIndex)" type="success">保存</el-button>
          </div>
          <div class="config-box shop-box">
            <div class="shop-item" v-for="(item,index) in shopSift" :key="index">
              <div class="shop-title">
                <h3>{{item.shopName}}</h3>
                <p>{{item.shopCateName}}</p>
              </div>
              <div class="shop-img">
                <img :src="item.logoUrl" alt>
              </div>
              <div class="shop-btn">
                <div class="shop-rank">
                  <el-button
                    type="primary"
                    round
                    icon="el-icon-back"
                    @click="sortleftGoods(index,'shopSift')"
                  ></el-button>
                  <el-button
                    type="primary"
                    round
                    icon="el-icon-back deg180"
                    @click="sortrightGoods(index,'shopSift')"
                  ></el-button>
                </div>
                <i class="el-icon-delete" @click="deleteItem(index, 'shopSift')"></i>
              </div>
            </div>
          </div>
        </div>
        <!-- 轻餐banner -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">轻餐banner</span>
            <el-upload
              action="#"
              :show-file-list="false"
              :on-success="imgSuccess"
              :before-upload="beforeImgUploadQing"
            >
              <el-button type="primary" icon="el-icon-plus" :loading="loadingBtn">添加</el-button>
            </el-upload>
            <el-button
              class="config-title-btn"
              type="success"
              @click="bannerSave(6+confinIndex,'bannerQing')"
            >保存</el-button>
          </div>
          <div class="config-box banner-box">
            <div class="banner-item" v-for="(item, index) in bannerQing" :key="index">
              <img class="banner-img" :src="item.imgUrl" alt>
              <a class="banner-del" @click="deleteItem(index, 'bannerQing')">
                <i class="el-icon-delete"></i>
              </a>
              <el-input
                class="banner-link"
                type="textarea"
                placeholder="请输入内容"
                v-model="item.linkUrl"
              ></el-input>
              <div class="banner-switchLink">
                <span>是否跳转</span>
                <el-switch v-model="item.jsJump"></el-switch>
              </div>
              <el-input
                class="banner-title"
                type="textarea"
                placeholder="这里是无跳转时可显示的文字提示..."
                v-model="item.remark"
              ></el-input>
              <div class="banner-rank">
                <span>顺序调整</span>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back"
                  @click="sortleftGoods(index,'bannerQing')"
                ></el-button>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back deg180"
                  @click="sortrightGoods(index,'bannerQing')"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
        <!-- 正餐banner -->
        <div class="c-config">
          <div class="config-title">
            <i class="config-title-icon"></i>
            <span class="config-title-text">正餐banner</span>
            <el-upload
              action="#"
              :show-file-list="false"
              :on-success="imgSuccess"
              :before-upload="beforeImgUploadZheng"
            >
              <el-button type="primary" icon="el-icon-plus" :loading="loadingBtn">添加</el-button>
            </el-upload>
            <el-button
              class="config-title-btn"
              type="success"
              @click="bannerSave(5+confinIndex,'bannerZheng')"
            >保存</el-button>
          </div>
          <div class="config-box banner-box">
            <div class="banner-item" v-for="(item, index) in bannerZheng" :key="index">
              <img class="banner-img" :src="item.imgUrl" alt>
              <a class="banner-del" @click="deleteItem(index, 'bannerZheng')">
                <i class="el-icon-delete"></i>
              </a>
              <el-input
                class="banner-link"
                type="textarea"
                placeholder="请输入内容"
                v-model="item.linkUrl"
              ></el-input>
              <div class="banner-switchLink">
                <span>是否跳转</span>
                <el-switch v-model="item.isJump"></el-switch>
              </div>
              <el-input
                class="banner-title"
                type="textarea"
                placeholder="这里是无跳转时可显示的文字提示..."
                v-model="item.remark"
              ></el-input>
              <div class="banner-rank">
                <span>顺序调整</span>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back"
                  @click="sortleftGoods(index,'bannerZheng')"
                ></el-button>
                <el-button
                  type="primary"
                  round
                  icon="el-icon-back deg180"
                  @click="sortrightGoods(index,'bannerZheng')"
                ></el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 添加商品弹窗 -->
    <el-dialog title="添加商品" :visible.sync="dialogAddgoods" width="1124px">
      <div class="dialogAdd-title">
        <el-form :inline="true" :model="filters" class="dialogAdd-search">
          <el-input
            v-model="filters.value"
            @keyup.enter.native="getGoodsList"
            placeholder="商家名称、商品名称"
          ></el-input>
          <el-button type="primary" @click="getGoodsList">搜索</el-button>
          <el-button @click="filters.value = ''">清空</el-button>
        </el-form>
      </div>
      <div class="dialogAdd-list" v-if="this.goodsList.length>0" v-loading="loadingGoods">
        <div
          class="goods-item"
          v-for="(item,index) in goodsList"
          :key="index"
          @click="addClassActive(item)"
        >
          <div class="goods-choice" :class="{active:item.id==indexActive}">
            <i class="el-icon-check"></i>
          </div>
          <div class="goods-img">
            <img :src="item.goodsSku.skuPic" alt>
            <div class="goods-img-text">
              <span>{{item.shopName}}</span>
            </div>
          </div>
          <div class="goods-title">
            <p>{{item.goodsSku.skuName}}</p>
            <span>剩余{{item.stockNum}}</span>
          </div>
          <div class="goods-price">
            <b>¥{{item.goodsPromotionRules.actAmount}}</b>
            <span>¥{{item.goodsSku.sellPrice}}</span>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" v-if="this.goodsList.length>0">
        <el-button @click="dialogAddgoods = false">取 消</el-button>
        <el-button type="primary" @click="confimAdd">确 定</el-button>
      </div>
      <div class="dialogAdd-default" v-else>
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有数据</p>
      </div>
    </el-dialog>
    <!-- 添加商家弹窗 -->
    <el-dialog title="添加商家" :visible.sync="dialogAddshop" width="1124px">
      <div class="dialogAdd-title">
        <el-form :inline="true" :model="filterShop" class="dialogAdd-search">
          <el-input
            v-model="filterShop.value"
            @keyup.enter.native="getShopList"
            placeholder="商家名称、商品名称"
          ></el-input>
          <el-button type="primary" @click="getShopList">搜索</el-button>
          <el-button @click="filterShop.value = ''">清空</el-button>
        </el-form>
      </div>
      <div class="dialogAdd-list" v-if="this.shopList.length>0" v-loading="loadingShop">
        <div
          class="shop-item"
          v-for="(item,index) in shopList"
          :key="index"
          @click="addClassActive(item)"
        >
          <div class="shop-choice" :class="{active:item.id==indexActive}">
            <i class="el-icon-check"></i>
          </div>
          <div class="shop-title">
            <h3>{{item.shopName}}</h3>
            <p>{{item.businessCate}}</p>
          </div>
          <div class="shop-img">
            <img :src="item.logoUrl" alt>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer" v-if="this.shopList.length>0">
        <el-button @click="dialogAddshop = false">取 消</el-button>
        <el-button type="primary" @click="addShop">确 定</el-button>
      </div>
      <div class="dialogAdd-default" v-else>
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有数据</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/assets/js/api.js";
import common from "@/assets/js/common.js";
export default {
  data() {
    return {
      loadingList: false,
      loadingShop: false,
      loadingGoods: false,
      cityIndex: 0,
      citys: [
        {
          city: "武汉市"
        },
        {
          city: "十堰市"
        },
        {
          city: "襄阳市"
        },
        {
          city: "黄冈市"
        }
      ],
      textarea: "",
      switchLink: "",
      //banner
      bannerHome: [],
      bannerQing: [],
      bannerZheng: [],
      //商品列表
      goodsRecommend: [],
      goodSeckill: [],
      shopSift: [],
      shopList: [],
      goodsList: [],
      //添加商品
      dialogAddgoods: false,
      filters: {
        value: ""
      },
      actId: "",
      //添加商家
      dialogAddshop: false,
      filterShop: {
        value: ""
      },
      //上传图片
      loadingBtn: false,
      imageUrl: "",
      indexActive: -1,
      currentDataName: "",
      currnetSelectData: "",
      configBox: "",
      confinIndex: 0
    };
  },
  mounted() {
    this.postActivetyList();
  },
  methods: {
    //监听父盒子宽度
    boxWidth() {
      let that = this;
      this.$nextTick(function() {
        that.configBox = that.$refs.boxStyle.$el.clientWidth + "px";
      });
    },
    //城市切换
    citySwitch(id) {
      this.cityIndex = id;
      this.postActivetyList();
      this.confinIndex = id * 6;
    },
    //获取活动列表
    postActivetyList() {
      this.loadingList = true;
      let cityIndex = this.cityIndex;
      api
        .findActivityRecommend({
          city: this.citys[cityIndex].city
        })
        .then(res => {
          let data = res.data;
          this.bannerHome = [];
          this.bannerZheng = [];
          this.bannerQing = [];
          this.goodsRecommend = [];
          this.goodSeckill = [];
          this.shopSift = [];
          if (res.code == 0) {
            let bannerList = data["at-0"];
            if (bannerList) {
              for (let i = 0; i < bannerList.length; i++) {
                if (bannerList[i].position == "xcx-home") {
                  this.bannerHome.push(bannerList[i] ? bannerList[i] : []);
                }
                if (bannerList[i].position == "xcx-category-48") {
                  this.bannerQing.push(bannerList[i] ? bannerList[i] : []);
                }
                if (bannerList[i].position == "xcx-category-1") {
                  this.bannerZheng.push(bannerList[i] ? bannerList[i] : []);
                }
              }
            } else {
              this.bannerHome = [];
              this.bannerZheng = [];
              this.bannerQing = [];
            }
            this.goodsRecommend = data["at-1"] ? data["at-1"] : [];
            this.goodSeckill = data["at-2"] ? data["at-2"] : [];
            this.shopSift = data["at-3"] ? data["at-3"] : [];
          }
          this.loadingList = false;
        })
        .catch(err => {
          console.log(err);
        });
    },
    savecommend(activityLayoutId, keyName) {
      var arr = [];
      let that = this;
      this[keyName].forEach(function(e, index) {
        var subobj = {
          sort: index,
          actId: e.actId,
          goodsSkuId: e.goodsSkuOut[0].id ? e.goodsSkuOut[0].id : e.goodsSku.id,
          city: that.citys[that.cityIndex].city,
          linkUrl: "",
          activityLayoutId: activityLayoutId,
          shopId: e.shopId
        };
        arr.push(subobj);
      });
      var obj = {
        activityLayoutId: activityLayoutId,
        activityRecommendList: arr
      }; 
      this.saveData(obj);
    },
    saveShop(activityLayoutId) {
      var arr = [];
      let that = this;
      this.shopSift.forEach(function(e, index) {
        var subobj = {
          sort: index,
          city: that.citys[that.cityIndex].city,
          linkUrl: "",
          activityLayoutId: activityLayoutId,
          shopId: e.id
        };
        arr.push(subobj);
      });
      var obj = {
        activityLayoutId: activityLayoutId,
        activityRecommendList: arr
      };
      this.saveData(obj);
    },
    saveData(datas) {
      api.saveActivityRecommend(datas).then(res => {
        if (res.code == 0) {
          this.postActivetyList();
          this.$message.success("保存成功"); 
        } else {
          this.$message.error(res.message); 
        }
      });
    },
    //监听图片上传状态变化
    imgSuccess(res, file) {},
    beforeImgUploadHome(file) {
      let that = this;
      let position = "xcx-home";
      that.loadingBtn = true;
      uploadImg(file).then(imgUrl => {
        var obj = {
          activityLayoutId: "",
          city: that.citys[that.cityIndex].city,
          imgUrl: imgUrl,
          isJump: true,
          linkUrl: "",
          remark: null,
          sort: that.bannerHome.length || 0
        };
        that.bannerHome.push(obj);
        that.loadingBtn = false;
      });
      return false;
    },
    beforeImgUploadQing(file) {
      let that = this;
      let position = "xcx-category-48";
      that.loadingBtn = true;
      uploadImg(file).then(imgUrl => {
        var obj = {
          activityLayoutId: "",
          city: that.citys[that.cityIndex].city,
          imgUrl: imgUrl,
          isJump: true,
          linkUrl: "",
          remark: null,
          sort: that.bannerQing.length || 0
        };
        that.bannerQing.push(obj);
        that.loadingBtn = false;
      });
      return false;
    },
    beforeImgUploadZheng(file) {
      let that = this;
      let position = "xcx-category-1";
      that.loadingBtn = true;
      uploadImg(file).then(imgUrl => {
        var obj = {
          activityLayoutId: "",
          city: that.citys[that.cityIndex].city,
          imgUrl: imgUrl,
          isJump: true,
          linkUrl: "",
          remark: null,
          sort: that.bannerZheng.length || 0
        };
        that.bannerZheng.push(obj);
        that.loadingBtn = false;
      });
      return false;
    },
    bannerSave(activityLayoutId, keyName) {
      for (let i = 0; i < this[keyName].length; i++) {
        this[keyName][i].activityLayoutId = activityLayoutId;
        this[keyName][i].city = this.citys[this.cityIndex].city;
        this[keyName][i].activityLayoutId = activityLayoutId;
      }
      var obj = {
        activityLayoutId: activityLayoutId,
        activityRecommendList: this[keyName]
      };
      this.saveData(obj);
    },
    showShop() {
      this.currnetSelectData = "";
      this.dialogAddshop = true;
    },
    addShop() {
      let flag = true;
      let that = this;
      that.shopSift.forEach(function(e, i) {
        if (e.id == that.currnetSelectData.id) {
          flag = false;
        }
      });
      if (flag) {
        that.shopSift.push(that.currnetSelectData);
      }
      that.currnetSelectData = "";
      that.dialogAddshop = false;
    },
    //监听添加商品按钮
    addGoods(actId, name) {
      this.goodsList = "";
      this.currnetSelectData = "";
      this.currentdataName = name;
      console.log("currentdataName", this.currentdataName);
      this.filters.value = "";
      this.dialogAddgoods = true;
      this.actId = actId;
    },
    confimAdd() {
      let flag = true;
      this[this.currentdataName].forEach(e => {
        if (e.id == this.currnetSelectData.id) {
          flag = false;
        }
      });
      if (flag && this.currnetSelectData) {
        this[this.currentdataName].push(this.currnetSelectData);
      }
      this.dialogAddgoods = false;
    },
    sortleftGoods(index, keyName) {
      if (index == 0) {
        return false;
      }
      for (var k in this) {
        if (k == keyName) {
          this[k].splice(
            index,
            1,
            ...this[k].splice(index - 1, 1, this[k][index])
          );
        }
      }
      for (let i = 0; i < this[keyName].length; i++) {
        this[keyName][i].sort = index;
        console.log("keyName", this[keyName]);
      }
    },
    sortrightGoods(index, keyName) {
      console.log(index);
      for (var k in this) {
        if (k == keyName) {
          if (index >= this[k].length - 1) {
            return false;
          }
          this[k].splice(
            index,
            1,
            ...this[k].splice(index + 1, 1, this[k][index])
          );
        }
      }
      for (let i = 0; i < this[keyName].length; i++) {
        console.log("keyName", this[keyName]);
        this[keyName][i].sort = index;
      }
    },
    //选定商家
    addClassActive(item) {
      this.currnetSelectData = item;
      this.indexActive = item.id;
      console.log("this.currnetSelectData", this.currnetSelectData);
    },
    //商品列表查询接口
    getGoodsList() {
      this.loadingGoods = true;
      this.indexActive = "";
      if (this.filters.value.trim() == "") {
        this.goodsList = "";
        this.loadingGoods = false;
        return false;
      }
      let cityIndex = this.cityIndex;
      api
        .selectGoodsList({
          params: {
            searcheKey: this.filters.value.trim(),
            city: this.citys[cityIndex].city,
            actId: this.actId
          }
        })
        .then(res => {
          let data = res.data;
          if (data.list !== null) {
            this.goodsList = data.list;
          } else {
            this.goodsList = "";
          }
          this.loadingGoods = false;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //商家列表查询接口
    getShopList() {
      this.loadingShop = true;
      this.indexActive = "";
      if (this.filterShop.value.trim() == "") {
        this.loadingShop = false;
        this.shopList = "";
        return false;
      }
      let cityIndex = this.cityIndex;
      api
        .list({
          params: {
            city: this.citys[cityIndex].city,
            searchKey: this.filterShop.value.trim()
          }
        })
        .then(res => {
          let data = res.data;
          if (data.list !== null) {
            this.shopList = data.list;
          } else {
            this.shopList = "";
          }
          this.loadingShop = false;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //监听保存接口
    save(actId) {
      let cityIndex = this.cityIndex;
      api
        .saveActivityRecommend({
          actId: this.actId,
          city: this.citys[cityIndex].city
        })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //监听排序
    rank(actId) {
      let cityIndex = this.cityIndex;
      api
        .sort(
          common.joint({
            actId: this.actId,
            city: this.citys[cityIndex].city
          })
        )
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //监听删除
    deleteItem(index, keyName) {
      this.$confirm("删除此项？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          for (var k in this) {
            if (k == keyName) {
              console.log(this[k]);
              this[k].splice(index, 1);
            }
          }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {});
    }
  }
};

var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        console.log(res);
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        console.log(err);
      });
  });
};
</script>
 
<style scoped lang="scss">
.c-city {
  padding: 20px;
  display: flex;
  border-bottom: 1px solid #e8e8e8;
  > a {
    display: block;
    line-height: 36px;
    padding: 0 16px;
    border-radius: 5px;
    margin-right: 47px;
    cursor: pointer;
    &.acitve,
    &:hover {
      border: 1px solid #5769f0;
      color: #5769f0;
    }
  }
}
.c-config {
  padding: 40px 30px 0 30px;
  border-bottom: 1px solid #e8e8e8;
  .config-title {
    display: flex;
    align-items: center;
    .config-title-icon {
      width: 6px;
      height: 24px;
      background: linear-gradient(
        0deg,
        rgba(87, 105, 240, 1) 0%,
        rgba(132, 170, 255, 1) 100%
      );
      border-radius: 3px;
    }
    .config-title-text {
      font-size: 24px;
      color: #222222;
      margin-left: 10px;
      flex: 1;
    }
    button:last-child {
      margin-left: 20px;
    }
  }
  .config-box {
    margin-top: 30px;
    display: flex;
    overflow-x: auto;
    padding-bottom: 40px;
  }
}
// banner 列表
.banner-item {
  flex-basis: 380px;
  width: 380px;
  flex-shrink: 0;
  margin-right: 20px;
  .banner-img {
    display: block;
    width: 100%;
    height: 270px;
    background: #eee;
    flex-shrink: 0;
  }
  .banner-del {
    display: block;
    width: 100%;
    height: 60px;
    line-height: 60px;
    background: #f1f2f8;
    text-align: center;
    cursor: pointer;
    font-size: 24px;
  }
  .banner-link {
    margin-top: 15px;
  }
  .banner-switchLink {
    margin-top: 15px;
    display: flex;
    align-items: center;
    > span {
      flex: 1;
      color: #333333;
      font-size: 18px;
    }
  }
  .banner-title {
    margin-top: 15px;
  }
  .banner-rank {
    margin-top: 15px;
    display: flex;
    align-items: center;
    > span {
      flex: 1;
      color: #333333;
      font-size: 18px;
    }
  }
}
// 商品信息列表
.goods-item {
  flex-basis: 245px;
  width: 245px;
  flex-shrink: 0;
  margin-right: 20px;
  .goods-img {
    width: 100%;
    height: 175px;
    background: #eee;
    position: relative;
    img {
      width: 100%;
      height: 175px;
    }
    .goods-img-text {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 40px;
      background: rgba(0, 0, 0, 0.5);
      padding: 0 12px;
      display: flex;
      align-items: center;
      color: #fff;
      & > span {
        flex: 1;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      & > i {
        font-size: 14px;
        cursor: pointer;
      }
    }
  }
  .goods-title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: #f1f2f8;
    padding: 0 12px;
    display: flex;
    box-sizing: border-box;
    > p {
      flex: 1;
      font-size: 18px;
      color: #333333;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    > span {
      color: #888787;
      font-size: 14px;
    }
  }
  .goods-price {
    margin: 15px 0;
    > b {
      color: #fc5e2d;
      font-size: 24px;
      margin-right: 15px;
    }
    > span {
      color: #999999;
      font-size: 20px;
      text-decoration: line-through;
    }
  }
}
//商家列表
.shop-item {
  flex-basis: 295px;
  flex-shrink: 0;
  width: 295px;
  margin-right: 20px;
  background: #f1f2f8;
  padding: 20px;
  box-sizing: border-box;
  .shop-title {
    > h3 {
      color: #222222;
      font-size: 20px;
      font-weight: bold;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    > p {
      color: #666666;
      font-size: 14px;
      margin-top: 12px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .shop-img {
    width: 100%;
    height: 175px;
    background: #eee;
    margin-top: 15px;
    img {
      width: 100%;
      height: 175px;
    }
  }
  .shop-btn {
    margin-top: 20px;
    display: flex;
    align-items: center;
    .shop-rank {
      flex: 1;
    }
    > i {
      cursor: pointer;
      font-size: 18px;
    }
  }
}
// 添加商品弹窗
.dialogAdd-title {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  > span {
    color: #595959;
    font-size: 16px;
    flex: 1;
  }
  .dialogAdd-search {
    display: flex;
    button {
      margin-left: 10px;
    }
  }
}
.dialogAdd-default {
  text-align: center;
  color: #b4b7c8;
  font-size: 16px;
  margin-top: 60px;
  img {
    height: 250px;
  }
}
.dialogAdd-list {
  display: flex;
  flex-wrap: wrap;
  .shop-item,
  .goods-item {
    margin-top: 30px;
    cursor: pointer;
    position: relative;
    .shop-choice,
    .goods-choice {
      position: absolute;
      right: 8px;
      top: 8px;
      width: 30px;
      height: 30px;
      background: #fff;
      border-radius: 100%;
      z-index: 9;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      > i {
        font-weight: bolder;
        font-size: 18px;
        color: #5769f0;
      }
      &.active {
        background: #5769f0;
        opacity: 1;
        > i {
          color: #fff;
        }
      }
    }
    &:hover {
      .shop-choice,
      .goods-choice {
        opacity: 1;
      }
    }
  }
}
</style>
 


