//自定义http请求
import http from './http.js';
import QS from 'qs';
const get = http.get;
const post = http.post;
let api = {
    login: p => post('app/auth/loginByPassword' + p),       //登陆
    findShopAccountTotal: p => post('manage/shopAccount/findShopAccountTotal', p),    //商户提现统计
    refreshShopAccountTotal: p => post('manage/shopAccount/refreshShopAccountTotal', p),    //刷新商户提现统计
    searchShopWithdrawList: p => post('manage/shopAccount/searchShopWithdrawList', p),  //提现记录
    approvePass: p => post('manage/shopAccount/approvePass', p),     //审批通过
    approveReject: p => post('manage/shopAccount/approveReject', p),    //审批驳回
    findShopAccountDetailById: p => post('manage/shopAccount/findShopAccountDetailById', p),   //店铺核销明细
    findAccountLog: p => post('manage/shopAccount/findAccountLog', p),     //店铺金额变更操作日志
    searchUserAdvanceList: p => post('manage/userAdvance/searchUserAdvanceList', p),    //用户提现记录
    batchApplyPass: p => post('manage/userAdvance/batchApplyPass', p),    //审批通过(支持批量)
    batchApplyReject: p => post('manage/userAdvance/batchApplyReject', p),   //审批驳回(支持批量)
    agreeRefund: p => post('manage/payRefund/agreeRefund', p),   //退款申请（同意退款）
    getList: p => post('manage/payRefund/getList', p),   //退款申请（表格数据）
    getTableList: p => post('manage/payRefund/getTableList', p),   //退款申请（退款详情）
    refuseRefund: p => post('manage/payRefund/refuseRefund', p),   //退款申请（退款驳回）
    exportRep: p => get('manage/payRefund/export', p),   //退款申请（导出报表）
    upload: p => post('manage/img/upload', p),   //文件上传
    selectGoodsList: p => get('manage/actGoodsSku/selectGoodsList', p),   //首页配置（添加商品）
    list: p => get('manage/shop/list', p),   //首页配置（添加商家）
    findActivityRecommend: p => post('manage/activity/findActivityRecommend', p),   //首页配置（活动列表）
    saveActivityRecommend: p => post('manage/activity/saveActivityRecommend', p),   //首页配置（保存活动）
    goodsSkuList: p => post('manage/goodsSku/pageListForAudit',p),//运营中心（商品审核列表）
    goodsSkuListAudit: p => post('manage/goodsSku/auditGoodsSku', p),//运营中心（商品审核  通过/驳回）
    storetotal: p => post('manage/detailed/getTableList', p),   //获取总数据
    areaList: p => get('manage/detailed/listAllShopZone', p), //专区列表
    storeList: p => post('manage/detailed/getList', p),   //商户列表
    getDetails: p => post('manage/detailed/getDetails', p),    //商户详情非订单列表
    getNoDetails: p => post('manage/detailed/getNoDetails', p),    //商户详情非订单列表
    showList: p => post('manage/orderInvoice/showList', p),      //发票信息列表
    agreeApply: p => post('manage/orderInvoice/agreeApply', p),       //发票审核同意/驳回
    rejectReason: p => post('manage/orderInvoice/getReason', p),     //发票审核驳回原因
    paymentList: p => post('manage/paymentType/getList', p),     //支付类型列表
    PlatformList: p => post('manage/Platform/getList', p),            //平台对账
    updateBusinesses: p => post('manage/detailed/updateBusinesses', p),   //商品明细（添加补贴）
    shopList: p => post('manage/BusinessListings/getList', p),     //商家列表
    dishList: p => post('manage/BusinessListings/getShopList', p),    //菜品列表
    orderList: p => post('manage/orderList/getList', p),    //订单列表 查询
    orderTotal: p => post('manage/orderList/getListTotal', p),   //订单列表 查询
    orderEditor: p => post('manage/orderList/updateOrderList', p),     //订单列表 查询
    shopEnterList: p => post('manage/shopEnter/showList', p),     //商家入驻 列表查询
    shopEnterOperation: p => post('manage/shopEnter/operationById', p),     //商家入驻 审核
    deleteBusinesses: p => post('manage/BusinessListings/deleteBusinesses', p),      //删除商家
    updateShopInfo: p => post('manage/BusinessListings/updateBusinesses', p),      //编辑商家信息
    updateClerk: p => post('manage/BusinessListings/updateClerk', p),                //增删核销员      
    updateGoods: p => post('manage/BusinessListings/updateGoods', p),                //编辑商品信息
    updateShopOwnerMap: p => post('manage/BusinessListings/updateShopOwnerMap', p),    //增删店铺主图
    deleteGoods: p => post('manage/BusinessListings/deleteGoods', p)                 //删除商品信息
};
export default api;


