<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button @click="tableAdd" type="primary" icon="el-icon-plus">添加</el-button>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="输入商品名称进行搜索"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button>清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain>批量删除</el-button>
        <el-table
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="id" label="ID" sortable></el-table-column>
          <el-table-column prop="name" label="角色名"></el-table-column>
          <el-table-column prop="code" label="Code"></el-table-column>
          <el-table-column prop="time" label="创建时间" sortable width="180px"></el-table-column>
          <el-table-column label="操作" width="180px">
            <template slot-scope="scope">
              <a class="operation-btn" @click="dialogEdit=true">编辑</a>
              <a class="operation-btn" @click="tableDel(scope.$index, scope.row)">删除</a>
              <a class="operation-btn" @click="dialogPrivilege=true">权限管理</a>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共110条，共10页</div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="100"
              layout="prev, pager, next, jumper"
              :total="1000"
            ></el-pagination>
          </div>
        </div>
      </div>
      <div v-else class="table-default">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>

    <!--弹窗 添加角色-->
    <el-dialog title="添加角色" :visible.sync="dialogAddRole" width="540px">
      <el-form :model="addForm">
        <el-form-item label="角色名" label-width="80px">
          <el-select v-model="addForm.role" placeholder="请选择">
            <el-option
              v-for="item in addForm.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Code" label-width="80px">
          <el-input v-model="addForm.code" autocomplete="off" placeholder="请输入Code"></el-input>
        </el-form-item>
        <el-form-item label="密码" label-width="80px">
          <el-input
            v-model="addForm.password"
            autocomplete="off"
            placeholder="请输入至少6位以上密码"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddRole = false">取消</el-button>
        <el-button type="primary" @click="addSubmit">保存</el-button>
      </div>
    </el-dialog>
    <!--弹窗 编辑-->
    <el-dialog title="编辑角色" :visible.sync="dialogEdit" width="540px">
      <el-form :model="form">
        <el-form-item label="角色名" label-width="80px">
          <el-select v-model="form.role" placeholder="请选择">
            <el-option
              v-for="item in form.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Code" label-width="80px">
          <el-input v-model="form.code" autocomplete="off" placeholder="请输入Code"></el-input>
        </el-form-item>
        <el-form-item label="密码" label-width="80px">
          <el-input
            v-model="form.password"
            autocomplete="off"
            placeholder="请输入至少6位以上密码"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEdit = false">取消</el-button>
        <el-button type="primary" @click="dialogEdit = false">保存</el-button>
      </div>
    </el-dialog>
    <!--弹窗 删除-->
    <el-dialog title="提示" :visible.sync="dialogDel" width="540px">
      <div style="color:#555555; font-size:20px; margin:30px 0;">是否确认删除此条？</div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogDel = false">取消</el-button>
        <el-button type="primary" @click="dialogDel = false">保存</el-button>
      </div>
    </el-dialog>
    <!--弹窗 权限管理-->
    <el-dialog title="添加权限" :visible.sync="dialogPrivilege" width="540px">
      <div class="privilege-title">
        <i class="privilege-dot"></i>
        <span>权限分类</span>
      </div>
      <div class="privilege-box">
        <el-tree
          :data="dataTree"
          show-checkbox
          default-expand-all
          node-key="id"
          ref="tree"
          highlight-current
        >
        <span class="custom-tree-node" slot-scope="{node,data}">
          <span :class="node.level==1 ? 'treeLabel' : ''" >{{data.label}}</span>
        </span>
        </el-tree>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogPrivilege = false">取消</el-button>
        <el-button type="primary" @click="dialogPrivilege = false">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogAddRole: false, //添加角色弹窗
      dialogEdit: false, //编辑弹窗
      dialogDel: false, //删除弹窗
      dialogPrivilege: false, //权限管理弹窗
      timeStart: "",
      timeEnd: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [
        {
          id: "1",
          name: "管理员",
          code: "admin",
          time: "2019-03-02 11:11:42"
        },
        {
          id: "2",
          name: "核销员",
          code: "will",
          time: "2019-03-02 11:11:42"
        }
      ],
      dataTree: [
        {
          label: "认证中心",
          children: [
            {
              label: "角色管理"
            },
            {
              label: "用户管理"
            },
            {
              label: "用户反馈"
            },
            {
              label: "商家入驻"
            },
            {
              label: "Token管理"
            }
          ]
        },
        {
          label: "运营中心",
          children: [
            {
              label: "商品上架"
            },
            {
              label: "商家列表"
            },
            {
              label: "订单列表"
            }
          ]
        },
        {
          label: "财务管理",
          children: [
            {
              label: "角色管理"
            },
            {
              label: "用户管理"
            },
            {
              label: "用户反馈"
            },
            {
              label: "商家入驻"
            },
            {
              label: "Token管理"
            }
          ]
        },
        {
          label: "运营监控"
        }
      ],
      form: {
        role: "",
        options: [
          {
            value: "选项1",
            label: "黄金糕"
          },
          {
            value: "选项2",
            label: "双皮奶"
          },
          {
            value: "选项3",
            label: "蚵仔煎"
          },
          {
            value: "选项4",
            label: "龙须面"
          },
          {
            value: "选项5",
            label: "北京烤鸭"
          }
        ],
        code: "",
        password: ""
      },
      addForm: {
        role: "",
        options: [
          {
            value: "选项1",
            label: "管理员"
          },
          {
            value: "选项2",
            label: "核销员"
          },
          {
            value: "选项3",
            label: "店长"
          }
        ],
        code: "",
        password: ""
      }
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    //获取用户列表
    getTable() {
      let para = {
        page: this.page,
        name: this.filters.name
      };
      this.listLoading = true;
      getUserListPage(para).then(res => {
        this.total = res.data.total;
        this.users = res.data.users;
        this.listLoading = false;
      });
    },
    //添加
    tableAdd() {
      this.dialogAddRole = true;
    },
    addSubmit() {},
    //删除
    tableDel() {
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { id: row.id };
          removeUser(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.getUsers();
          });
        })
        .catch(() => {});
    }
  }
};
</script>


<style scoped lang="scss">
.privilege-title {
  width: 100%;
  height: 44px;
  background: #5769f0;
  display: flex;
  align-items: center;
  > i.privilege-dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: #fff;
    margin-left: 20px;
    border-radius: 100%;
  }
  > span {
    flex: 1;
    text-align: right;
    margin-right: 20px;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
  }
}
.privilege-box {
  width: 100%;
  height: 360px;
  overflow-y: auto;
  border: 1px solid #eeeeee;
  padding: 20px;
  .custom-tree-node {
    align-items: center;
    justify-content: space-between;
    padding-right: 8px;
    .treeLabel::before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 16px;
      height: 19px;
      margin-top: -2px;
      margin-right: 2px;
      background: url("../../../../assets/images/icon-role-add.png") no-repeat;
    }
  }
}
</style>



