<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeStart"
            unlink-panels
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="pickTime"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
          ></el-date-picker>
          <div class="selectBox">
            <span>审核状态</span>
            <el-select
              v-model="selectStatus"
              @change="changeStatus"
              clearable
              placeholder="状态筛选"
            >
              <el-option
                v-for="item in Status"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                v-model="filters.name"
                placeholder="输入门店名称进行搜索"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <!-- <el-button type="primary" plain @click="batchprocessing(false)">
          批量驳回
        </el-button>
        <el-button type="primary" plain @click="batchprocessing(true)">
          批量通过
        </el-button> -->
        <el-table
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{ prop: 'date', order: 'descending' }"
          v-loading="tableloading"
        >
          <!-- <el-table-column type="selection" width="55"></el-table-column> -->
          <el-table-column prop="id" label="ID" sortable></el-table-column>
          <el-table-column
            prop="userName"
            width="120"
            label="联系人"
          ></el-table-column>
          <el-table-column
            prop="shopName"
            width="220"
            label="门店名称"
          ></el-table-column>
          <el-table-column
            prop="address"
            width="220"
            label="详细地址"
          ></el-table-column>
          <el-table-column
            prop="shopCate"
            width="200"
            label="经营品类"
          ></el-table-column>
          <el-table-column
            prop="status"
            width="160"
            label="审核状态"
            :formatter="formatStatus"
          ></el-table-column>
          <el-table-column
            prop="createTime"
            label="创建时间"
            width="220"
            sortable
          ></el-table-column>
          <el-table-column width="180" label="操作" fixed="right">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="openDetail(scope.row)"
              >
                查看详情
              </el-button>

              <el-button
                type="text"
                size="small"
                @click="operating(false, scope.row.id)"
                :disabled="scope.row.status != 0"
              >
                驳回
              </el-button>
              <el-button
                type="text"
                size="small"
                @click="operating(true, scope.row.id)"
                :disabled="scope.row.status != 0"
              >
                通过
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{ total }}条，共{{ pages }}页</div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>

    <!--弹窗 商家入驻详情-->
    <el-dialog
      title="商家入驻详情"
      v-if="enterList"
      :visible.sync="dialogEnter"
      width="786"
    >
      <ul class="dialogEnter-info">
        <li v-for="(item, index) in enterListMap" :key="index">
          <div class="info-title">{{ item.title }}:</div>
          <div class="info-content">{{ enterList[item.value] }}</div>
        </li>
        <template>
          <el-radio-group style="display:block;"></el-radio-group>
          <el-tabs tab-position="left">
            <el-tab-pane label="营业执照">
              <div class="img-small" @click="clickImg($event)">
                <img :src="enterList.licensePic" alt />
                <a class="img-btn"></a>
              </div>
            </el-tab-pane>
            <el-tab-pane label="卫生许可证">
              <div class="img-small" @click="clickImg($event)">
                <img :src="enterList.healthPic" alt />
                <a class="img-btn"></a>
              </div>
            </el-tab-pane>
            <el-tab-pane label="门头照">
              <div class="img-small" @click="clickImg($event)">
                <img :src="enterList.doorPic" alt />
                <a class="img-btn"></a>
              </div>
            </el-tab-pane>
            <el-tab-pane label="Logo">
              <div class="img-small" @click="clickImg($event)">
                <img :src="enterList.logoPic" alt />
                <a class="img-btn"></a>
              </div>
            </el-tab-pane>
          </el-tabs>
        </template>
      </ul>
    </el-dialog>
    <!-- 驳回弹窗 -->
    <el-dialog
      v-loading="loadingReject"
      :close-on-click-modal="false"
      @close="rejectClose"
      title="驳回"
      :visible.sync="dialogFail"
      width="680px"
    >
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="驳回原因">
          <el-select v-model="formReject.select" placeholder="请选择">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select == 0">
          <el-input
            type="textarea"
            placeholder="请输入驳回原因"
            v-model="formReject.textarea"
            :maxlength="formReject.maxlength"
            :rows="3"
            style="width:402px;"
            show-word-limit
          >
          </el-input>
        </el-form-item>
        <el-form-item label="上传图片">
          <el-upload
            v-loading="uploadLoading"
            :on-exceed="limitError"
            :limit="5"
            ref="rejectUpload"
            action="#"
            list-type="picture-card"
            :on-success="imgSuccess"
            :http-request="beforeImgUploadHome"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="“" />
          </el-dialog>
        </el-form-item>
        <el-form-item label="发送短信">
          <el-radio v-model="formReject.isSend" label="1">是</el-radio>
          <el-radio v-model="formReject.isSend" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="formRejectCancle">取 消</el-button>
        <el-button
          type="primary"
          @click="formRejectSubmit"
          :loading="uploadLoading"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!--放大图片-->
    <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
  </div>
</template>

<script>
import api from "../../../../assets/js/api";
import BigImg from "@/components/common/BigImg.vue";
export default {
  name: "Enter",
  data() {
    return {
      dialogEnter: false,
      timeStart: [],
      timeEnd: "",
      filters: {
        name: ""
      },
      tableData: [],
      selectStatus: null,
      Status: [
        {
          value: 0,
          label: "待审批"
        },
        {
          value: 1,
          label: "审批通过"
        },
        {
          value: 2,
          label: "审批不通过"
        }
      ],
      enterList: null,
      enterListMap: [
        {
          title: "联系人",
          value: "userName"
        },
        {
          title: "门店名称",
          value: "shopName"
        },
        {
          title: "门店电话",
          value: "mobile"
        },
        {
          title: "详细地址",
          value: "address"
        },
        {
          title: "经营品类",
          value: "shopCate"
        },
        {
          title: "创建时间",
          value: "createTime"
        }
      ],
      showImg: false,
      pageSize: 10,
      pages: 0,
      total: 0,
      currentPage: 1,
      tableloading: false,
      loadingReject: false,
      formReject: {
        select: null,
        option: [
          {
            value: "图文不符",
            label: "图文不符"
          },
          {
            value: "菜品类目与商家类目不统一",
            label: "菜品类目与商家类目不统一"
          },
          {
            value: 0,
            label: "其他原因"
          }
        ],
        textarea: "",
        isSend: "1",
        maxlength: "30",
        remnant: 0
      },
      dialogFail: false,
      dialogVisible: false,
      dialogImageUrl: null,
      shopid: null,
      dialogImageArr: [],
      uploadLoading: false
    };
  },
  components: {
    BigImg
  },
  methods: {
    openDetail(item) {
      //打开详情
      this.dialogEnter = true;
      this.enterList = item;
    },
    formatStatus(row, column, cellValue, index) {
      return this.Status.map(ele => {
        if (ele.value == cellValue) {
          return ele.label;
        }
      });
    },
    search() {
      //搜索
      this.currentPage = 1;
      this.getShopList();
    },
    pickTime() {
      //选中时间
      this.currentPage = 1;
      this.getShopList();
    },
    changeStatus() {
      //选中状态
      this.currentPage = 1;
      this.getShopList();
    },
    empty() {
      //清空搜索
      this.filters.name = null;
      this.getShopList();
    },
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getShopList();
    },
    //点击放大图片
    clickImg(e) {
      this.showImg = true;
      this.imgSrc = e.currentTarget.children[0].src;
    },
    //点击隐藏放大图片弹窗
    viewImg() {
      this.showImg = false;
    },
    getShopList() {
      this.tableloading = true;
      api
        .shopEnterList({
          beginTime: this.timeStart[0],
          endTime: this.timeStart[1],
          page: this.currentPage,
          rows: 10,
          searchKey: this.filters.name,
          status: this.selectStatus
        })
        .then(res => {
          let data = res.data;
          if (res.code === 0) {
            this.tableData = data.list;
            this.total = data.total;
            this.pages = data.pages;
            this.tableloading = false;
          }
        })
        .catch(err => {
          this.$confirm("接口报错"+err.message)
            .then(_ => {
              this.tableloading = false;
            })
            .catch(_ => {});
        });
    },
    limitError() {
      //上传数量限制
      this.$message({
        type: "error",
        message: "最多上传五个，你已超出"
      });
    },
    imgSuccess() {},
    beforeImgUploadHome(param) {
      let that = this;
      this.uploadLoading = true;
      uploadImg(param.file).then(imgUrl => {
        that.dialogImageUrl = imgUrl;
        param.file.imgUrl = imgUrl;
        that.dialogImageArr.push(param.file);
        that.uploadLoading = false;
      });
      return false;
    },
    handleRemove(file, fileList) {
      this.dialogImageArr = this.dialogImageArr.filter(ele => {
        ele.uid == file.uid;
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    formRejectCancle() {
      //取消驳回
      this.dialogFail = false;
      this.formReject = {
        select: null,
        option: [
          {
            value: 1,
            label: "卫生许可证，营业执照不符"
          },
          {
            value: 2,
            label: "经营品类与实际不符"
          },
          {
            value: 0,
            label: "其他原因"
          }
        ],
        textarea: null,
        isSend: "1",
        maxlength: 30,
        remnant: 0
      };
    },
    rejectClose() {
      //驳回弹窗关闭时
      // 清除表单数据
      this.$set(this.formReject, "select", null);
      this.$set(this.formReject, "textarea", null);
      this.$set(this.formReject, "isSend", "1");
      this.$set(this.formReject, "remnant", 0);
      this.dialogImageUrl = null;
      this.dialogImageArr = [];
      this.$refs.rejectUpload.clearFiles();
    },
    formRejectSubmit() {
      //驳回弹窗确认
      if (
        this.formReject.select == null ||
        (this.formReject.select == "0" && !this.formReject.textarea)
      ) {
        return this.$message.warning("请填写驳回原因");
      }
      let urlArr = [];
      this.loadingReject = true;
      this.dialogImageArr.forEach((value, i) => {
        urlArr.push(value.imgUrl);
      });
      let imgUrlStr = urlArr.join(",");
      this.aduitItem(
        this.shopid,
        false,
        this.formReject.select || this.formReject.textarea,
        imgUrlStr,
        this.formReject.isSend
      );
    },
    operating(status, id) {
      //单个审核
      this.shopid = null;
      this.shopid = id;
      if (status) {
        this.$confirm("此操作将通过审核, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.tableloading = true;
          this.aduitItem(id, true);
        });
      } else {
        this.dialogFail = true;
      }
    },
    aduitItem(id, flag, result, pivcs, smsIsSend) {
      //审核接口
      //shopEnterId  商家id
      //aduitStatus  "1"表示通过，"0"为不通过
      //memo 驳回理由
      //pictures 驳回图片
      //sendMsg 是否发送短信  0 不发送  1 发送
      api
        .shopEnterOperation({
          shopEnterId: id,
          status: flag,
          memo: result,
          pictures: pivcs,
          sendMsg: parseInt(smsIsSend)
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            this.getShopList();
            this.shopid = null;
            this.dialogFail = false;
            this.loadingReject = false;
            this.tableloading = false;
            this.$message({
              type: "success",
              message: "操作成功"
            });
          } else {
            this.getShopList();
            this.shopid = null;
            this.dialogFail = false;
            this.loadingReject = false;
            this.tableloading = false;
            this.$message({
              type: "error",
              message: res.message
            });
          }
        })
        .catch(err => {
          this.$confirm("接口报错" + err.message)
            .then(_ => {
              this.dialogFail = false;
              this.loadingReject = false;
              this.tableloading = false;
            })
            .catch(_ => {});
        });
    }
  },
  mounted() {
    this.getShopList();
  }
};
var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        this.$confirm("接口报错"+err.message)
          .then(_ => {})
          .catch(_ => {});
      });
  });
};
</script>

<style scoped lang="scss">
.dialogEnter-info {
  > li {
    display: flex;
    margin-bottom: 32px;
    .info-title {
      color: #333333;
      font-size: 20px;
      line-height: 26px;
      width: 120px;
      font-weight: 400;
      text-align: right;
    }
    .info-content {
      flex: 1;
      margin-left: 23px;
      font-size: 20px;
      color: #666;
      font-weight: 400;
      line-height: 26px;
    }
  }
}
</style>

<style lang="scss" scoped>
.img-small {
  background-color: #e8e8e8;
}
.dialogEnter-info {
  .el-tabs__item {
    font-size: 20px;
    font-weight: 400;
    color: #333;
    width: 127px;
    padding: 0 7px 0 0;
    height: 58px;
    line-height: 26px;
  }
  .el-tabs__item.is-active {
    color: #5769f0;
  }
  .el-tabs__active-bar {
    background-color: #5769f0;
  }
  .el-tabs--left .el-tabs__active-bar.is-left {
    height: 26px !important;
    width: 3px;
  }
  .el-tabs__nav-wrap::after {
    background-color: #e8e8e8;
  }
  .el-tabs--left .el-tabs__nav-wrap.is-left::after,
  .el-tabs--left .el-tabs__nav-wrap.is-right::after,
  .el-tabs--right .el-tabs__nav-wrap.is-left::after,
  .el-tabs--right .el-tabs__nav-wrap.is-right::after {
    width: 1px;
  }
}
.img-small {
  margin-left: 10px;
}
</style>


