<template>
  <div class="c-box">
    <div class="orderData" v-loading="totalLoading">
      <div class="orderData_item">
        <span>订单总金额</span>
        <span class="item_num">{{orderSum}}</span>
        <span>(元)</span>
      </div>
      <div class="orderData_item">
        <span>订单总数量</span>
        <span class="item_num">{{orderCount}}</span>
        <span>(单)</span>
      </div>
    </div>
    <div class="c-table">
      <!-- 表格option -->
      <div class="table-title">
        <div class="title-left">
          <el-button
            type="primary"
            icon="el-icon-upload2"
            style="margin-right:28px;"
            @click="exportExcel"
          >导出</el-button>
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>位置筛选</span>
            <el-cascader
              v-model="area"
              :options="options"
              :show-all-levels="false"
              :filterable="true"
              :clearable="true"
            ></el-cascader>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" style="float:right;">
            <el-form-item>
              <el-input
                v-model="keywords"
                @keyup.enter.native="search"
                placeholder="請输入商家名称进行搜索"
                style="width: 260px;"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="shopLoading"
          :data="tableData"
          style="width: 100%;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID" width="70" fixed>
            <template slot-scope="scope">{{scope.$index+1}}</template>
          </el-table-column>
          <el-table-column prop="shopName" label="商家名称" width="140"></el-table-column>
          <el-table-column prop="city" label="所在城市" width="120"></el-table-column>
          <el-table-column prop="prefectureName" label="所在专区" width="130"></el-table-column>
          <el-table-column prop="orderCount" label="订单数量" width="140" sortable :sort-method="sortByCount"></el-table-column>
          <el-table-column prop="orderSum" label="订单金额" width="140" sortable :sort-method="sortBySum"></el-table-column>
          <el-table-column prop="orderQuantityCount" label="非订单数量" width="140" sortable :sort-method="sortByNoCount"></el-table-column>
          <el-table-column prop="orderQuantitySum" label="非订单金额" width="140" sortable :sort-method="sortByNoSum"></el-table-column>
          <el-table-column prop="mobile" label="商家电话" width="130"></el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="170" sortable></el-table-column>
          <el-table-column label="操作" fixed="right" width="160">
            <template slot-scope="scope">
              <el-button
                type="text"
                @click="detail(scope.row.id,scope.row.orderCount,scope.row.orderSum,scope.row.orderQuantityCount,scope.row.orderQuantitySum)"
              >查看详情</el-button>
              <el-button type="text" @click="addSubsidy(scope.row.id)">添加补贴</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="shopLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
    <!--弹窗 商户详情-->
    <el-dialog title="商户详情" :visible.sync="dialogDetail" width="1056px">
      <div class="dialogDetail-title">
        <div class="title_item">
          总金额:
          <b>¥{{storeSum}}</b>元
        </div>
        <div class="title_item">
          总订单:
          <b>{{storeCount}}</b>单
        </div>
      </div>
      <div class="dialog-table">
        <el-table v-loading="orderLoading" :data="detailList" border style="width: 100%">
          <el-table-column prop="orderCode" label="订单号" width="200"></el-table-column>
          <el-table-column prop="skuName" label="商品名称"></el-table-column>
          <el-table-column prop="serviceAmount" label="单笔服务费" width="100"></el-table-column>
          <el-table-column prop="typeTxt" label="订单类型"></el-table-column>
          <el-table-column prop="realUnitPrice" label="支付金额"></el-table-column>
          <el-table-column prop="realShopUnitPrice" label="商家实际到账金额"></el-table-column>
          <el-table-column prop="createTime" label="核销时间"></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{dTotal}}条，共{{dPages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="detailPage"
              :current-page.sync="dPage"
              :page-count="dPages"
              layout="prev, pager, next, jumper"
              :total="dTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
      <div class="dialog-table">
        <el-table v-loading="changeLoading" :data="changeList" border style="width: 100%">
          <el-table-column prop="operateAmount" label="变更金额"></el-table-column>
          <el-table-column prop="remark" label="变更内容"></el-table-column>
          <el-table-column label="变更方式">
            <template slot-scope="scope">
              <a v-if="scope.row.operateType == 1">减少</a>
              <a v-if="scope.row.operateType == 2">增加</a>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="变更时间"></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{cTotal}}条，共{{cPages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="changePage"
              :current-page.sync="cPage"
              :page-count="cPages"
              layout="prev, pager, next, jumper"
              :total="cTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>
    <!-- 弹窗 添加补贴 -->
    <el-dialog title="添加补贴" :visible.sync="dialogSubsidy" width="600px">
      <el-form ref="formSubsidy" label-width="100px">
        <el-form-item label="补贴方式">
          <el-select v-model="formSubsidy.select" placeholder="请选择">
            <el-option
              v-for="item in formSubsidy.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item> 
        <el-form-item label="补贴金额">
          <el-input v-model="formSubsidy.money" placeholder="请输入金额"></el-input>
        </el-form-item>
        <el-form-item label="补贴内容">
          <el-input v-model="formSubsidy.text" placeholder="请输入补贴内容"></el-input>          
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogSubsidy = false">取 消</el-button>
        <el-button type="primary" @click="subsidy">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "../../../../assets/js/api.js";
import common from "../../../../assets/js/common.js";
export default {
  data() {
    return {
      dialogSubsidy: false,
      formSubsidy: {
        select: "",
        option: [
          {
            value: 0,
            label: "增加"
          },
          {
            value: 1,
            label: "扣除"
          }
        ],
        money: "",
        text: ""
      },
      id: "",
      orderSum: 0,
      orderCount: 0,
      keywords: "",
      position: "",
      dialogDetail: false,
      beginTime: "",
      endTime: "",
      storeSum: 0, //订单金额
      storeCount: 0, //订单数量
      rows: 10,
      total: 1,
      pages: 1,
      page: 1,
      dTotal: 1,
      dPages: 1,
      dPage: 1,
      cTotal: 1,
      cPages: 1,
      cPage: 1,
      totalLoading: true,
      shopLoading: true,
      orderLoading: true,
      changeLoading: true,
      area: [],
      dateArray: "",
      options: [],
      tableData: [],
      detailList: [],
      changeList: []
    };
  },
  watch: {
    area(val) {
      if (val.length == 2) {
        let idx1 = val[0],
          idx2 = val[1];
        this.position = this.options[idx1].children[idx2].label;
      } else {
        this.position = "";
      }
      this.shopLoading = true;
      this.page = 1;
      this.storeList();
    }
  },
  methods: {
    pickTime(e) {
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.totalLoading = true;
      this.shopLoading = true;
      this.page = 1;
      this.totalData();
      this.storeList();
    },
    totalData() {
      //总数据
      api
        .storetotal({
          beginTime: this.beginTime,
          endTime: this.endTime
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0) {
            this.orderCount = data[0].orderCount; //数量
            this.orderSum = data[0].orderSum; //金额
            this.totalLoading = false;
          }
        })
        .catch(err => {});
    },
    areaOption() {
      //下拉框专区
      api
        .areaList()
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.length > 0) {
            let obj = {};
            data.forEach((e, i) => {
              obj = {
                value: i,
                label: e.city,
                children: []
              };
              e.shopZoneItem.forEach((k, j) => {
                obj.children.push({
                  value: j,
                  label: k.name
                });
              });
              this.options.push(obj);
            });
          }
        })
        .catch(err => {});
    },
    storeList() {
      //商家列表
      api
        .storeList({
          beginTime: this.beginTime,
          endTime: this.endTime,
          keywords: this.keywords,
          position: this.position,
          page: this.page,
          rows: this.rows
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            for (let i = 0; i < data.list.length; i++) {
              data.list[i].createTime = common.formatTime(
                new Date(data.list[i].createTime)
              );
            }
            this.pages = data.pages;
            this.total = data.total;
            this.tableData = data.list ? data.list : [];
            this.shopLoading = false;
          }
        })
        .catch(err => {});
    },
    exportExcel() {
      //导出
      location.href =
        "/api/manage/detailed/export?beginTime=" +
        this.beginTime +
        "&endTime=" +
        this.endTime +
        "&keywords=" +
        this.keywords +
        "&position=" +
        this.position +
        "&token=" + 
        "Bearer " + localStorage.token;
    },
    search() {
      //搜索
      this.shopLoading = true;
      this.page = 1;
      this.storeList();
    },
    // 清空
    empty(){
      this.shopLoading = true;
      this.keywords = '';
      this.page = 1;
      this.storeList();
    },
    detail(id, count, sum, noCount, noSum) {
      //点击查看详情
      this.dialogDetail = true;
      this.orderLoading = true;
      this.changeLoading = true;
      this.id = id;
      this.cPage = 1;
      this.dPage = 1;
      this.storeSum = Number(sum) + Number(noSum);
      this.storeCount = Number(count) + Number(noCount);
      this.storeSum = this.storeSum.toFixed(2);
      this.storeCount = Math.floor(this.storeCount);
      this.detailTable();
      this.changeTable();
    },
    detailTable() {
      //明细详情列表
      api
        .getDetails({
          id: this.id,
          page: this.dPage,
          rows: this.rows
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            this.dPages = data.pages;
            this.dTotal = data.total;
            this.detailList = data.list ? this.flagType(data.list) : [];
            this.orderLoading = false;
          }
        })
        .catch(err => {});
    },
    changeTable() {
      //变更列表
      api
        .getNoDetails({
          id: this.id,
          page: this.cPage,
          rows: this.rows
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            this.cPages = data.pages;
            this.cTotal = data.total;
            this.changeList = data.list ? data.list : [];
            this.changeLoading = false;
          }
        })
        .catch(err => {});
    },
    listPage(val) {
      //列表分页
      this.shopLoading = true;
      this.page = val;
      this.storeList();
    },
    detailPage(val) {
      //详情分页
      this.orderLoading = true;
      this.dPage = val;
      this.detailTable();
    },
    changePage(val) {
      //变更分页
      this.changeLoading = true;
      this.cPage = val;
      this.changeTable();
    },
    flagType(list) {
      //判断订单类型
      // flagType:标记类型(1普通型  4砍价型  5秒杀型 6抽奖型 7团购型)'
      list.forEach((e, i) => {
        e.typeTxt = (type => {
          if (type == 1) return "普通型";
          if (type == 4) return "砍价型";
          if (type == 5) return "秒杀型";
          if (type == 6) return "抽奖型";
          if (type == 7) return "团购型";
        })(e.flagType);
      });
      return list;
    },
    //添加补贴弹窗
    addSubsidy(rowData){
      this.formSubsidy.select = "";
      this.formSubsidy.money = "";
      this.formSubsidy.text = "";
      this.dialogSubsidy = true;
      this.id = rowData;
    },
    //补贴弹窗 确定
    subsidy(){ 
      if (!this.formSubsidy.select && this.formSubsidy.select != 0) return this.$message.warning('请选择补贴方式');
      if (!this.formSubsidy.money) return this.$message.warning('请填写补贴金额');
      if (!this.formSubsidy.text) return this.$message.warning('请填写补贴内容');
      api.updateBusinesses({
        shopId: this.id,
        type: this.formSubsidy.select,
        remake: this.formSubsidy.text,
        occurAmount: this.formSubsidy.money
      }).then(res =>{  
        if(res.code == 0){
          this.$message.success('恭喜你，添加补贴成功');
          this.storeList();
        }else{
          this.$message.error('提示，添加补贴失败');
        } 
        this.dialogSubsidy = false;
      }).catch(err =>{  
        this.dialogSubsidy = false;
      }) 
    },
    sortByCount(obj1, obj2) {     //订单数量排序
      let str1 = obj1.orderCount;
      let str2 = obj2.orderCount;
      return str1 - str2;
    },
    sortBySum(obj1, obj2) {     //订单金额排序
      let str1 = obj1.orderSum;
      let str2 = obj2.orderSum;
      return str1 - str2;
    },
    sortByNoCount(obj1, obj2) {     //非订单数量排序
      let str1 = obj1.orderQuantityCount;
      let str2 = obj2.orderQuantityCount;
      return str1 - str2;
    },
    sortByNoSum(obj1, obj2) {     //非订单金额排序
      let str1 = obj1.orderQuantitySum;
      let str2 = obj2.orderQuantitySum;
      return str1 - str2;
    }
  },
  mounted() {
    this.storeList();
    this.totalData();
    this.areaOption();
  }
};
</script>

<style scoped lang="scss">
.orderData {
  display: flex;
  margin-bottom: 24px;
  padding: 40px 0;
  background-color: #fff;
  border-radius: 6px;
  .orderData_item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    font-size: 18px;
    .item_num {
      font-size: 32px;
      color: #000000;
      margin: 0 14px 0 20px;
    }
    &:first-child {
      border-right: 1px solid #bbbbbb;
    }
  }
}
.dialogDetail-title {
  display: flex;
  color: #555;
  font-size: 24px;
  font-weight: 500;
  line-height: 26px;
  margin-bottom: 24px;
  .title_item {
    &:first-child {
      margin-right: 80px;
    }
    b {
      color: #5769f0;
    }
  }
}
</style>




