<template>
  <div class="c-box">
    <div class="c-merchant">
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b v-if="item.number == null||item.number == ''">0</b>
            <b v-else>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button
            @click="exportExcel"
            type="primary"
            icon="el-icon-upload2"
            style="margin-right:28px;"
          >导出</el-button>
          <el-date-picker
            v-model="timeSection"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>审核状态</span>
            <el-select v-model="selectScreen" placeholder="请选择" @change="getTableData('reset')">
              <el-option
                v-for="item in optionScreen"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                v-model="filters.name"
                @keyup.enter.native="getTableData('reset')"
                placeholder="订单编号、所属门店、电话"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData('reset')">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain @click="batchAgree">批量同意</el-button>
        <el-button type="primary" plain @click="batchDisagree">批量驳回</el-button>
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%;margin-top:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" :selectable="checkboxInit" width="40" fixed></el-table-column>
          <el-table-column type="expand" width="40" fixed>
            <template slot-scope="props">
              <table class="table-expand">
                <thead>
                  <tr>
                    <td width="200px">商品金额</td>
                    <td width="200px">商品名称</td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.row.orderItems" :key="index">
                    <td>￥{{item.goodsPrice}}</td>
                    <td>{{item.goodsSkuName}}</td>
                  </tr>
                </tbody>
              </table>
            </template>
          </el-table-column>
          <el-table-column prop="outTradeNo" label="订单编号" width="140"></el-table-column>
          <el-table-column prop="createTime" label="提交申请时间" width="170" sortable></el-table-column>
          <el-table-column label="订单类型" width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.orderType==1">订单</span>
              <span v-if="scope.row.orderType==2">商城订单</span>
              <span v-if="scope.row.orderType==3">商城券</span>
            </template>
          </el-table-column>
          <el-table-column prop="totalFee" label="订单支付金额" width="140" sortable></el-table-column>
          <el-table-column prop="userName" label="用户账户" width="150"></el-table-column>
          <el-table-column label="审核状态" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.status=='0'">
                  <span class="green">待审批</span>
                </template>
                <template v-if="scope.row.status=='1'">
                  <span class="red">已通过</span>
                </template>
                <template v-if="scope.row.status=='2'">
                  <span class="grgy">已驳回</span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="申请来源" width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.type==1">小程序</span>
              <span v-if="scope.row.type==2">APP</span>
            </template>
          </el-table-column>
          <el-table-column prop="shop.shopName" label="所属门店" width="160"></el-table-column>
          <el-table-column label="支付类型" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payType=='0'">
                  <i class="icon icon-zengsong"></i>
                  <span>免费赠送</span>
                </template>
                <template v-if="scope.row.payType=='1'">
                  <i class="icon icon-jinbi"></i>
                  <span>享七金币支付</span>
                </template>
                <template v-if="scope.row.payType=='2'">
                  <i class="icon icon-weixin"></i>
                  <span>微信支付</span>
                </template>
                <template v-if="scope.row.payType=='3'">
                  <i class="icon icon-zhifubao"></i>
                  <span>支付宝支付</span>
                </template>
                <template v-if="scope.row.payType=='4'">
                  <i class="icon icon-yue"></i>
                  <span>余额支付</span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="applyReason" label="申请原因" width="120"></el-table-column>
          <el-table-column prop="otherReason" label="其他原因" width="120"></el-table-column>
          <el-table-column label="操作" width="100" fixed="right">
            <template slot-scope="scope">
              <span v-if="scope.row.status=='0'">
                <el-button type="text" @click="consent(scope.row)">同意</el-button>
                <el-button type="text" @click="reject(scope.row)">驳回</el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled>同意</el-button>
                <el-button type="text" disabled>驳回</el-button>
              </span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="page"
              :page-count="pages"
              :page-size="rows"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="loadingTable">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
    <!--弹窗 驳回-->
    <el-dialog title="驳回" :visible.sync="dialogFail" width="680px">
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="驳回原因">
          <el-select v-model="formReject.select" placeholder="请选择">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select==4">
          <el-input
            type="textarea"
            placeholder="请输入退款原因"
            v-model="formReject.textarea"
            maxlength="30"
            show-word-limit
            style="width:402px;"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="上传图片">
            <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt>
            </el-dialog>
        </el-form-item>-->
        <el-form-item label="发送短信">
          <el-radio v-model="formReject.isSend" label="1">是</el-radio>
          <el-radio v-model="formReject.isSend" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFail = false">取 消</el-button>
        <el-button type="primary" @click="formRejectSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

  <script>
import api from "@/assets/js/api.js";
import formatTime from "@/assets/js/common.js";
export default {
  data() {
    return {
      dialogFail: false,
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [],
      page: 1, //分页初始页
      pages: 0, //总页数
      rows: 10, //每页行数
      total: 0, //总条数
      orderList: [],
      selectScreen: "",
      optionScreen: [
        {
          value: "",
          label: "全部状态"
        },
        {
          value: 0,
          label: "待审批"
        },
        {
          value: 1,
          label: "已通过"
        },
        {
          value: 2,
          label: "已驳回"
        }
      ],
      //上传图片
      dialogImageUrl: "",
      dialogVisible: false,
      loadingTable: false,
      loadingBtn: false,
      timeSection: "",
      _beginTime: "", //开始时间
      get beginTime() {
        return this._beginTime;
      },
      set beginTime(value) {
        this._beginTime = value;
      },
      endTime: "", //结束时间
      multipleSelection: [], //checkbox 存放
      //驳回
      formReject: {
        select: "",
        option: [
          {
            value: 1,
            label: "系统出错"
          },
          {
            value: 2,
            label: "恶意刷单"
          },
          {
            value: 3,
            label: "超过时间"
          },
          {
            value: 4,
            label: "其他原因"
          }
        ],
        textarea: "",
        isSend: "1"
      }
    };
  },
  mounted() {
    this.getTableData();
    this.getList();
  },
  methods: {
    //获取退款订单信息
    getList() {
      api
        .getTableList()
        .then(res => {
          if (res.code == 1) {
            let data = res.data,
              orderList = [];
            orderList = [
              {
                iconImg: "icon-order1",
                number: data[0].todayFefundTotal,
                title: "今日退款订单总数(笔)"
              },
              {
                iconImg: "icon-money1",
                number: data[0].todayFefundAmount,
                title: "今日退款订单总金额(元)"
              },
              {
                iconImg: "icon-order2",
                number: data[0].historicalFefundTotal,
                title: "历史退款订单总数(笔)"
              },
              {
                iconImg: "icon-money2",
                number: data[0].historicalFefundAmount,
                title: "历史退款订单总金额(元)"
              }
            ];
            this.orderList = orderList;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //获取退款申请表格数据
    getTableData(types) {
      this.loadingTable = true;
      if (types == "reset") {
        this.page = 1;
      }
      api
        .getList({
          page: this.page,
          rows: this.rows,
          status: this.selectScreen,
          beginTime: this.beginTime,
          endTime: this.endTime,
          keywords: this.filters.name.trim()
        })
        .then(res => {
          let data = res.data;
          if (data) {
            for (let i = 0; i < data.list.length; i++) {
              data.list[i].createTime = formatTime.formatTime(
                new Date(data.list[i].createTime)
              );
              data.list[i].totalFee = Number(data.list[i].totalFee).toFixed(
                2
              );
              for(let j = 0; j < data.list[i].orderItems.length; j++){
                data.list[i].orderItems[j].goodsPrice = Number(data.list[i].orderItems[j].goodsPrice).toFixed(
                  2
                );
              }
            }
            this.tableData = data.list ? data.list : [];
            this.total = data.total;
            this.page = data.page;
            this.rows = data.rows;
            this.pages = data.pages;
            this.loadingTable = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //监听同意按钮
    consent(rowData) {
      this.$confirm("此操作将确定同意用户退款, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.postAgreeRefund(rowData);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "操作失败"
          });
        });
    },
    // 获取同意接口
    postAgreeRefund(choseData) {
      let arr = [];
      if (choseData instanceof Array) {
        arr = choseData;
      } else {
        arr.push(choseData.id);
      }
      let id = "";
      if (arr.length > 1) {
        id = arr.join(",");
      } else {
        id = arr[0];
      }
      let params = {
        ids: id
      };
      api
        .agreeRefund(params)
        .then(res => {
          if (res.message) {
            this.$message({
              type: "success",
              message: "操作成功!"
            });
            this.getTableData();
          } else {
            this.$message.error("操作失败");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //批量同意弹窗
    batchAgree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("此操作无法撤回，将批量同意用户提现, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.id);
            });
            this.postAgreeRefund(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "操作失败"
            });
          });
      } else {
        this.$notify({
          title: "提示",
          message: "当前未选中任何用户，请至少选择一名用户!",
          type: "warning"
        });
      }
    },
    //监听驳回弹窗
    reject(row) {
      this.dialogFail = true;
      this.id = row.id;
      this.formReject.select = "";
      this.formReject.textarea = "";
      this.formReject.isSend = "1";
    },
    //监听驳回弹窗确定按钮
    formRejectSubmit() {
      console.log(this.formReject.select);
      let content = "";
      if (!this.formReject.select)
        return this.$message.error("提示，退款信息不能为空");
      if (this.formReject.select == 4 && !this.formReject.textarea)
        return this.$message.error("请输入其他原因");
      if (this.formReject.select == 4) {
        content = this.formReject.textarea;
      }else{
        content = this.formReject.option[this.formReject.select-1].label;
      } 
      let arr = [];
      arr.push(this.id);
      this.postRefuseRefund(arr, content);
    },
    //获取驳回接口
    postRefuseRefund(arr, content) { 
      let id = "";
      if (arr.length > 1) {
        id = arr.join(",");
      } else {
        id = arr[0];
      }
      api
        .refuseRefund({
          ids: id,
          content: content,
          isSend: this.formReject.isSend
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("审核驳回");
            this.dialogFail = false;
            this.getTableData();
          } else {
            this.dialogFail = false;
            this.$message.error(res.message);
          }
        })
        .catch(err => {
          this.dialogFail = false;
          this.$message.error(err);
        });
    },
    //批量驳回
    disagreeByGroup() {
      let arr = [];
      this.multipleSelection.forEach((v, k) => {
        arr.push(v.id);
      });
      this.postRefuseRefund(arr);
    },
    //批量驳回弹窗
    batchDisagree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("此操作无法撤回，将批量驳回用户提现, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.disagreeByGroup();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "操作失败"
            });
          });
      } else {
        this.$notify({
          title: "提示",
          message: "当前未选中任何用户，请至少选择一名用户!",
          type: "warning"
        });
      }
    },
    //分页跳转
    handleCurrentChange(val) {
      this.getTableData();
    },
    //导出报表
    exportExcel() {
      let params = {},
        str = "";
      params = {
        excle: true,
        status: this.selectScreen,
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.filters.name.trim()
      };
      for (let key in params) {
        str += key + "=" + params[key] + "&";
      }
      str = str.substring(0, str.length - 1);
      location.href = "/api/manage/payRefund/export?" + str;
    },
    //清空
    empty() {
      this.filters.name = "";
      this.getTableData("reset");
    },
    //筛选时间
    pickTime(e) {
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.getTableData("reset");
    },
    //判断checkbox是否可勾选
    checkboxInit(row, index) {
      if (row.status == 0) {
        return 1; //可勾选
      } else {
        return 0; //不可勾选
      }
    },
    //监听checkbox
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
    // 上传图片
    // handleRemove(file, fileList) {
    //   console.log(file, fileList);
    // }
    // handlePictureCardPreview(file) {
    //   this.dialogImageUrl = file.url;
    //   this.dialogVisible = true;
    // }
  }
};
</script>

<style scoped lang="scss">
.c-merchant ul {
  display: flex;
  li {
    flex: 1;
    height: 141px;
    background: #fff;
    border-radius: 6px;
    margin: 0 24px 24px 0;
  }
  li:last-child {
    margin-right: 0;
  }
  .income-li {
    > p {
      color: #666;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      text-align: center;
      margin-top: 42px;
    }
    > h3 {
      text-align: center;
      line-height: 26px;
      > b {
        color: #5769f0;
        font-size: 20px;
        font-weight: 400;
        line-height: 60px;
      }
      > span {
        color: #ffc000;
        font-size: 20px;
        font-weight: 400;
        line-height: 60px;
      }
    }
  }
  .order-li:nth-child(3),
  .order-li:nth-child(4) {
    .order-img {
      background: #eef2fc;
    }
  }
  .order-li {
    display: flex;
    align-items: center;
    .order-img {
      width: 66px;
      height: 66px;
      background: #fff2ca;
      border-radius: 100%;
      margin-left: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      i {
        display: inline-block;
        background-image: url(../../../../assets/images/sprites-order.png);
        &.icon-order1 {
          width: 28px;
          height: 32px;
          background-position: -58px -66px;
        }
        &.icon-order2 {
          width: 28px;
          height: 32px;
          background-position: -10px -66px;
        }
        &.icon-money1 {
          width: 36px;
          height: 36px;
          background-position: -10px -10px;
        }
        &.icon-money2 {
          width: 36px;
          height: 36px;
          background-position: -66px -10px;
        }
      }
    }
    > h3 {
      flex: 1;
      margin-right: 29px;
      text-align: right;
      > b {
        font-size: 32px;
        color: #333;
        font-weight: bold;
        margin-top: 25px;
        line-height: 26px;
      }
      > p {
        font-size: 16px;
        color: #555;
        font-weight: 400;
        margin-top: 9px;
      }
    }
  }
}

.form-remnant {
  position: absolute;
  top: 20px;
  color: #999;
  text-align: right;
  width: 392px;
}
</style>



