<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="dateArray"
            unlink-panels
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            @change="pickTime"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                class="searchInput"
                v-model="filters.name"
                placeholder="输入店铺名称或商品名称进行搜索"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTablelist">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <el-button type="primary" plain @click="batchprocessing(false)"
          >批量驳回</el-button
        >
        <el-button type="primary" plain @click="batchprocessing(true)"
          >批量通过</el-button
        >
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%; margin-top:20px;"
          :default-sort="{ prop: 'date', order: 'descending' }"
          @selection-change="selectItems"
        >
          <el-table-column
            type="selection"
            width="60"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="id"
            label="ID"
            width="70"
            align="center"
            sortable
          ></el-table-column>
          <el-table-column
            prop="shopName"
            label="店铺名称"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="skuName"
            label="商品名称"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="skuType"
            label="商品类型"
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column prop="marketPrice" label="商品价格" align="center">
            <template slot-scope="scope">
              <p v-show="scope.row.sellPrice">
                <span>门市价:{{ scope.row.sellPrice }}元</span>
              </p>
              <p v-show="scope.row.singlyPrice">
                <span>单买价:{{ scope.row.singlyPrice }}元</span>
              </p>
              <p v-show="scope.row.spikePrice">
                <span>秒杀价:{{ scope.row.spikePrice }}元</span>
              </p>
              <p v-show="scope.row.actPrice">
                <span>底价:{{ scope.row.actPrice }}元</span>
              </p>
            </template></el-table-column
          >
          <el-table-column
            prop="updateTime"
            label="提交时间"
            width="170"
            align="center"
            sortable
          ></el-table-column>
          <el-table-column
            prop="skuPic"
            label="商品主图"
            width="160"
            align="center"
          >
            <template slot-scope="scope">
              <img
                @click="selectimg(scope.row.skuPic)"
                class="goodsImg"
                :src="scope.row.skuPic"
              />
            </template>
          </el-table-column>
          <el-table-column
            prop="content"
            label="图文内容"
            align="center"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <div class="goodsContent" v-html="scope.row.content"></div>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="operating(false, scope.row.id)"
                >驳回</el-button
              >
              <el-button
                type="text"
                size="small"
                @click="operating(true, scope.row.id)"
                >通过</el-button
              >
              <!-- <el-button type="text" size="small">删除</el-button> -->
              <el-button type="text" size="small" @click="showDetail(scope.row)"
                >查看详情</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{ total }}条，共{{ pages }}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!-- 主图详情弹窗 -->
    <el-dialog :visible.sync="dialogVisibleImgPic" :append-to-body="true">
      <img width="100%" :src="previewImg" alt />
    </el-dialog>
    <!-- 商品详情弹窗 -->
    <el-dialog title="商品详情" :visible.sync="detailVisible" width="50%">
      <el-dialog :visible.sync="dialogVisibleImg" :append-to-body="true">
        <img width="100%" :src="goodsContent.skuPic" alt />
      </el-dialog>
      <div class="dialog_content">
        <div class="dialog_item" v-for="(item, key) in titledatas" :key="key">
          <div class="dia_item_l">{{ item }}：</div>
          <div v-if="key == 'skuPic'" class="dia_item_Img">
            <img
              :src="goodsContent[key]"
              @click="dialogVisibleImg = true"
              alt
            />
          </div>
          <div v-else-if="key == 'goodsSkuPicsList'" class="dia_item_Imgs">
            <img
              v-for="item in goodsContent[key]"
              :key="item"
              :src="item"
              alt
            />
          </div>
          <div
            v-else-if="key == 'content'"
            class="dia_item_r"
            v-html="goodsContent[key]"
          ></div>
          <div v-else class="dia_item_r">{{ goodsContent[key] }}</div>
        </div>
      </div>
    </el-dialog>
    <!-- 驳回弹窗 -->
    <el-dialog
      v-loading="loadingReject"
      :close-on-click-modal="false"
      @close="rejectClose"
      title="驳回"
      :visible.sync="dialogFail"
      width="680px"
    >
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="驳回原因">
          <el-select v-model="formReject.select" placeholder="请选择">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select == 0">
          <el-input
            type="textarea"
            placeholder="请输入驳回原因"
            v-model="formReject.textarea"
            :maxlength="formReject.maxlength"
            @input="descInput"
            :rows="3"
            style="width:402px;"
            show-word-limit
          >
          </el-input>
        </el-form-item>
        <el-form-item label="上传图片">
          <el-upload
            :on-exceed="limitError"
            :limit="5"
            ref="rejectUpload"
            action="#"
            list-type="picture-card"
            :on-success="imgSuccess"
            :http-request="beforeImgUploadHome"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" :modal='false'>
            <img width="100%" :src="dialogImageUrl" alt="“" />
          </el-dialog>
        </el-form-item>
        <el-form-item label="发送短信">
          <el-radio v-model="formReject.isSend" label="1">是</el-radio>
          <el-radio v-model="formReject.isSend" label="0">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="formRejectCancle">取 消</el-button>
        <el-button
          type="primary"
          :loading="uploadLoading"
          @click="formRejectSubmit"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template> 
<script>
import api from "../../../../assets/js/api";
import common from "../../../../assets/js/common.js";
export default {
  name: "newStock",
  data() {
    return {
      formReject: {
        select: null,
        option: [
          {
            value: "图文不符",
            label: "图文不符"
          },
          {
            value: "菜品类目与商家类目不统一",
            label: "菜品类目与商家类目不统一"
          },
          {
            value: 0,
            label: "其他原因"
          }
        ],
        textarea: "",
        isSend: "1",
        maxlength: "30",
        remnant: 0
      },
      dateArray: null,
      timeEnd: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [],
      goodsContent: [],
      detailVisible: false,
      dialogFail: false,
      dialogDel: false,
      page: 1, //表格当前页数
      pages: 0,
      total: 0,
      rows: 10,
      aduitItemData: null,
      beginTime: null,
      endTime: null,
      aduitDatas: [],
      dialogVisible: false,
      dialogImageUrl: null,
      dialogImageArr: [],
      titledatas: {
        shopName: "店铺名称",
        shopType: "店铺类型",
        skuName: "商品名称",
        skuType: "商品类型",
        actPrice: "商品底价",
        sellPrice: "商品门市价",
        singlyPrice: "商品单买价",
        spikePrice: "秒杀价",
        skuPic: "商品主图",
        content: "商品描述",
        goodsSkuPicsList: "详情图片"
      },
      loadingReject: false,
      loadingTable: false,
      dialogVisibleImg: false, //详情预览图片
      dialogVisibleImgPic: false, //列表预览图片
      previewImg: null, //选中预览图片
      uploadLoading: false
    };
  },
  methods: {
    selectimg(img) {
      //预览图片
      this.dialogVisibleImgPic = true;
      this.previewImg = img;
    },
    limitError() {
      //上传数量限制
      this.$message({
        type: "error",
        message: "最多上传五个，你已超出"
      });
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getTablelist();
    },
    handleClose2(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    pickTime(e) {
      //选中时间
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.getTablelist();
    },
    getTablelist() {
      //请求列表数据
      this.loadingTable = true;
      api
        .goodsSkuList({
          page: this.page,
          rows: this.rows,
          startTime: this.beginTime,
          endTime: this.endTime,
          skuName: this.filters.name
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.list) {
            this.pages = data.pages;
            this.total = data.total;
            this.tableData = data.list;
            this.loadingTable = false;
          }
        })
        .catch(err => {});
    },
    operating(status, id) {
      //单个审核
      this.aduitDatas = [];
      this.aduitDatas.push(id);
      if (status) {
        this.$confirm("此操作将通过审核, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.aduitItem([id], true);
          this.$message({
            type: "success",
            message: "操作成功"
          });
        });
      } else {
        this.dialogFail = true;
      }
    },
    batchprocessing(status) {
      if (status) {
        //批量审核
        let msg = status
          ? "此操作将批量通过审核,是否继续?"
          : "此操作将批量驳回,是否继续?";
        this.$confirm(msg, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.aduitItem(this.aduitDatas, status);
        });
      } else {
        this.dialogFail = true;
      }
    },
    aduitItem(id, flag, result, pivcs, smsIsSend) {
      //审核接口
      //ids  数据必须为数组
      //aduitStatus  "2"表示通过，其余为不通过
      //aduitItemrejectResult 驳回理由
      //rejectPics 驳回图片
      //smsIsSend 是否发送短信  0 不发送  1 发送
      api
        .goodsSkuListAudit({
          ids: id,
          aduitStatus: flag ? "2" : "3",
          rejectResult: result,
          rejectPics: pivcs,
          smsIsSend: smsIsSend
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            this.getTablelist();
            this.dialogFail = false;
            this.aduitDatas = [];
            this.loadingReject = false;
          }
        })
        .catch(err => {});
    },
    selectItems(selection) {
      //获得选中数据
      this.aduitDatas = [];
      if (selection.length > 0) {
        for (let i = 0; i < selection.length; i++) {
          this.aduitDatas.push(selection[i].id);
        }
      }
    },
    showDetail(data) {
      //商品详情
      this.goodsContent = data;
      this.detailVisible = true;
    },
    formRejectSubmit() {
      //驳回弹窗确认
      if (
        this.formReject.select == null ||
        (this.formReject.select == "0" && !this.formReject.textarea)
      ) {
        return this.$message.warning("请填写驳回原因");
      }
      let urlArr = [];
      this.loadingReject = true;
      this.dialogImageArr.forEach((value, i) => {
        urlArr.push(value.imgUrl);
      });
      let imgUrlStr = urlArr.join(",");
      this.aduitItem(
        this.aduitDatas,
        false,
        this.formReject.select || this.formReject.textarea,
        imgUrlStr,
        this.formReject.isSend
      );
    },
    rejectClose() {
      //驳回弹窗关闭时
      // 清除表单数据
      this.$set(this.formReject, "select", null);
      this.$set(this.formReject, "textarea", null);
      this.$set(this.formReject, "isSend", "1");
      this.$set(this.formReject, "remnant", 0);
      this.dialogImageUrl = null;
      (this.dialogImageArr = []), this.$refs.rejectUpload.clearFiles();
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      // this.dialogVisible = true;
    },
    //清空
    empty() {
      this.filters.name = "";
      this.getTablelist();
    },
    descInput() {
      //监听字数
      this.formReject.remnant = this.formReject.textarea.length;
    },
    imgSuccess() {},
    beforeImgUploadHome(param) {
      let that = this;
      this.uploadLoading = true;
      uploadImg(param.file).then(imgUrl => {
        that.dialogImageUrl = imgUrl;
        param.file.imgUrl = imgUrl;
        that.dialogImageArr.push(param.file);
        this.uploadLoading = false;
      });
      return false;
    },
    handleRemove(file, fileList) {
      this.dialogImageArr = this.dialogImageArr.filter(ele => {
        ele.uid == file.uid;
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    formRejectCancle() {
      //取消驳回
      this.dialogFail = false;
      this.formReject = {
        select: null,
        option: [
          {
            value: 1,
            label: "图文不符"
          },
          {
            value: 2,
            label: "菜品类目与商家类目不统一"
          },
          {
            value: 0,
            label: "其他原因"
          }
        ],
        textarea: null,
        isSend: "1",
        maxlength: 30,
        remnant: 0
      };
    }
  },
  mounted() {
    this.getTablelist();
  }
};
var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        console.log(err);
      });
  });
};
</script>

<style scope="scoped" lang="scss">
.goodsImg {
  width: 60px;
  height: 60px;
}
.searchInput {
  input {
    width: 250px;
  }
}
.goodsContent {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-height: 60px;
  img {
    max-width: 60px;
    max-height: 60px;
  }
}

.goodsContent div {
  text-align: left;
  margin-left: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.el-dialog__header {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  .el-dialog__title {
    font-size: 18px;
  }
  .el-dialog__headerbtn {
    font-size: 18px;
    top: 16px;
  }
}

.dialog_content {
  max-height: 600px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 50px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    background: rgba(0, 0, 0, 0.1);
  }
  .dialog_item {
    display: flex;
    flex-direction: row;
    padding-top: 30px;
    &:first-child {
      padding-top: 0;
    }
    .dia_item_l {
      width: 140px;
      text-align: right;
      color: #333333;
    }
    .dia_item_r {
      color: #666666;
      width: 760px;
    }
    .dia_item_Img {
      width: 760px;
      img {
        width: 170px;
        height: 170px;
        margin: 0 18px 18px 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .dia_item_Imgs {
      width: 760px;
      img {
        width: 170px;
        height: 170px;
        margin: 0 18px 18px 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
</style>


