<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">           
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span>审核状态</span>
            <el-select v-model="applyStatus" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true">
            <el-form-item>
              <el-input v-model="keywords" @keyup.enter.native="search" placeholder="請输入用户电话或订单号进行搜索" style="width: 260px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search">搜索</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0"> 
        <el-button type="primary" plain @click="operateFunc('multi', 1)">批量同意</el-button>
        <el-button type="primary" plain @click="operateFunc('multi', 2)">批量驳回</el-button>
        <el-table
          v-loading="tableLoading"
          :data="tableData"
          style="width: 100%; margin-top:20px"
          :default-sort="{prop: 'date', order: 'descending'}"
          @selection-change="selectChange"
        >
          <el-table-column type="selection" width="55" fixed="left" :selectable="selectable"></el-table-column>
          <el-table-column prop="orderCode" label="订单编号" min-width="200"></el-table-column>
          <el-table-column prop="createTime" label="申请时间" width="200" sortable></el-table-column>
          <el-table-column prop="mobile" label="开票手机号" width="160"></el-table-column>
          <el-table-column prop="invoiceAmount" label="发票金额" width="120" sortable></el-table-column>
          <el-table-column prop="billType" label="发票类别" width="120"></el-table-column>
          <el-table-column prop="invoiceOpen" label="发票抬头" width="120"></el-table-column>
          <el-table-column prop="dutyParagraph" label="发票税号" width="180"></el-table-column>
          <el-table-column prop="detailAddress" label="收货地址" width="180"></el-table-column>
          <el-table-column prop="chatName" label="收件人" width="100"></el-table-column>
          <el-table-column prop="addressMobile" label="收件手机号" width="180"></el-table-column>
          <el-table-column prop="email" label="电子邮箱地址" width="180"></el-table-column>
          <el-table-column label="审批状态" width="120">
            <template slot-scope="scope">
              <div class="table-scope">                 
                <span v-if="scope.row.applyStatus==2" class="green">已通过</span>
                <span v-if="scope.row.applyStatus==3" class="red">已驳回</span>
                <span v-if="scope.row.applyStatus==1" class="grgy">待审核</span>
              </div>
            </template>      
          </el-table-column>
          <el-table-column label="操作" width="120" fixed="right">
            <template slot-scope="scope"> 
              <span v-if="scope.row.applyStatus==1">
                <el-button type="text" @click="operateFunc('single', 1, scope.row.id)">同意</el-button>
                <el-button type="text" @click="operateFunc('single', 2, scope.row.id)">驳回</el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled>同意</el-button>
                <el-button type="text" disabled>驳回</el-button>
              </span>
            </template> 
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">共{{total}}条，共{{pages}}页</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              :total="total"
              layout="prev, pager, next, jumper"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!-- 暂无数据 -->
      <div v-else class="table-default" v-loading="tableLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p>暂时没有此类数据哦～</p>
      </div>
    </div>
    <!--弹窗 通过-->
    <el-dialog title="通过" :visible.sync="dialogPass" width="540px" v-loading="passLoading">
      <div class="dialog-info">是否确认通过审核？</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPass = false">取 消</el-button>
        <el-button type="primary" @click="pass">确 定</el-button>
      </span>
    </el-dialog>
    <!--弹窗 驳回-->
    <el-dialog title="驳回" :visible.sync="dialogForbidden" width="900px" v-loading="rejectLoading">
      <div class="dialogForbidden-info">
        <div class="rejectArea">
          <span class="rejectArea_tit">驳回原因</span>
          <el-select v-if="!isCustom" v-model="rejectSel" placeholder="请选择">
            <el-option
              v-for="item in rejectOpt"
              :key="item.id"
              :label="item.value"
              :value="item.id"
            ></el-option>
          </el-select>
          <el-input 
            v-if="isCustom"
            type="textarea"
            :autosize="true"
            placeholder="请输入内容"
            v-model="textarea"
            maxlength="100"
          >
          </el-input>
        </div>
        <div class="rejectMessage">
          <span>发送短信</span>
          <template>
            <el-radio v-model="radio" label="1">是</el-radio>
            <el-radio v-model="radio" label="2">否</el-radio>
          </template>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogForbidden = false">取 消</el-button>
        <el-button type="primary" @click="reject">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      id: '',
      ids: '',
      checkType: '',
      dialogPass: false,
      dialogForbidden: false,
      beginTime: "",
      endTime: "",
      keywords: "",
      applyStatus: "",
      rows: 10,
      page: 1,
      pages: 1,
      total: 1,
      rejectSel: "",
      isCustom: false,
      textarea: "",
      radio: "1",
      tableLoading: true,
      passLoading: false,
      rejectLoading: false,
      options: [       //审核状态选项
        {
          id: '',
          value: "全部"
        },
        {
          id: 1,
          value: "待审核"
        },
        {
          id: 2,
          value: "已通过"
        },
        {
          id: 3,
          value: "已驳回"
        }
      ],
      rejectOpt: [],
      dateArray: "",
      tableData: []
    };
  },
  watch: {
    applyStatus(val) {    //状态筛选
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    rejectSel(val) {
      this.isCustom = val == 'custom' ? true : false;
    }
  },
  methods: {
    selectable(row, index) {
      return row.applyStatus == 1 ? true : false;
    },
    pickTime(e) {
      if(e == null) {
        this.beginTime = '';
        this.endTime = '';
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    billList() {    //发票列表
      api.showList({
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.keywords,
        applyStatus: this.applyStatus,
        page: this.page,
        rows: this.rows        
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.tableData = data.list ? this.operateList(data.list) : [];
          this.pages = data.pages;
          this.total = data.total;
          this.tableLoading = false;
        }
      }).catch(err => {});
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        let str1 = '', str2 = '';
        if(e.initialType == 1) str1 = '个人';
        if(e.initialType == 2) str1 = '企业';
        if(e.sourceType == 1) str2 = '电子发票';
        if(e.sourceType == 2) str2 = '纸质发票';
        e.billType = str1 + str2;
      });
      return arr;
    },
    rejectReason() {    //驳回原因
      api.rejectReason().then(res => {
        if(res.code == 0 && res.data) {
          let data = res.data;
          data.forEach((e, i) => {
            this.rejectOpt.push({
              id: i,
              value: e.reason
            });
          });
          this.rejectOpt.push({
            id: 'custom',
            value: "其他（可填原因）"
          })
        }
      }).catch(err => {});
    },
    search() {      //搜索
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    //清空
    empty(){
      this.tableLoading = true;
      this.keywords = "";
      this.page = 1;
      this.billList();
    },
    operateFunc(checkType, type, id) {      //点击操作
      if(checkType == 'multi' && this.ids == '') return this.$message.warning('请至少选择一项');
      id && (this.id = id);
      this.checkType = checkType;
      if(type == 1) return this.dialogPass = true;
      if(type == 2) {
        this.rejectSel = "";
        this.textarea = "";
        this.isCustom = false; 
        this.dialogForbidden = true;
      }
    },
    pass() {        //审核通过
      this.passLoading = true;
      let ids = this.checkType == 'multi' ? this.ids : this.id;
      api.agreeApply({
        ids: ids,
        idsLength: ids.toString().split(',').length,
        applyStatus: 2
      }).then(res => {
        if(res.code == 1) {
          this.dialogPass = false;
          this.passLoading = false;
          this.$message.success('审核通过');
          this.tableLoading = true;
          this.billList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {})
    },
    reject() {      //审核驳回
      let content = '';
      this.rejectOpt.forEach((e, i) => {
        if(e.id == this.rejectSel) {
          this.content = e.id == 'custom' ? this.textarea : e.value;
        }
      })
      if(this.content == '') {
        this.$message.error('请先选择原因');
        return false;
      };
      this.rejectLoading = true;
      let ids = this.checkType == 'multi' ? this.ids : this.id;
      api.agreeApply({
        ids: ids,
        idsLength: ids.toString().split(',').length,
        applyStatus: 3,
        isSend: this.radio,
        content: this.content
      }).then(res => {
        if(res.code == 1) {
          this.dialogForbidden = false;
          this.rejectLoading = false;
          this.$message.success('审核驳回');
          this.tableLoading = true;
          this.billList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {})
    },
    selectChange(selection) {      //操作复选框
      let ids = '';
      selection && selection.forEach((e, i) => {
        ids += e.id + ',';
      });
      ids = ids.substring(0 ,ids.length - 1);
      this.ids = ids;
    },
    listPage(val) {       //翻页
      this.tableLoading = true;
      this.page = val;
      this.billList();
    }
  },
  mounted() {
    this.billList();
    this.rejectReason();
  }
};
</script>

<style scoped lang="scss">
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
  .rejectArea {
    display: flex;
    .rejectArea_tit {
      margin-right: 20px;
    }
    .el-textarea {
      width: 600px;
    }
  }
  .rejectMessage {
    margin-top: 30px;
  }
}
</style>



