<template>
  <div class="left-nav">
    <ul class="sidebar-el-menu">
      <a @click="collapseChage()">
        <i class="icon iconfont">
          <span class="icon-home"></span>
        </i>
      </a>
      <li v-for="(item, index) in menuList" :key="index">
        <router-link
          :to="item.path"
          :class="{'active': isActive(item.path)}"
          effect="dark"
          content="item.link"
          placement="right"
          hash="item.link"
          @click.native="current(item.link, index)"
        >
          <el-tooltip
            :disabled="disabled"
            class="item"
            effect="dark"
            :content="item.link"
            placement="right"
          >
            <i class="icon iconfont">
              <span :class="item.icon"></span>
            </i>
          </el-tooltip>
          <div class="title">{{item.link}}</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import bus from "@/components/common/bus.js";
import { mapState, mapMutations } from 'vuex';
export default {
  data() {
    return { 
      disabled: true,
      collapse: false //侧边栏折叠
    };
  },
  computed: {
    ...mapState(['menuList'])
  },
  methods: {
    ...mapMutations(['setParentName', 'setChildName']),
    isActive(path) {
      if (this.$route.fullPath.indexOf(path) > -1) {
        return true;
      } else {
        return path === this.$route.fullPath;
      }
    },
    // 侧边栏折叠
    collapseChage() {
      this.disabled = !this.disabled;
      this.collapse = !this.collapse;
      bus.$emit("collapse", this.collapse);
    },
    current(link, index) {
      this.setParentName(link);
      this.setChildName(this.menuList[index].array[0].link);
    }
  },
  mounted() {
    if (document.body.clientWidth < 1500) {
      this.collapseChage();
    }
  }
};
</script>

<style scoped lang="scss">
.left-nav {
  display: block;
  position: absolute;
  left: 0;
  top: 64px;
  bottom: 0;
  overflow-y: scroll;
  background: #282b33;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 210px;
}
.left-nav > ul a {
  display: flex;
  width: 100%;
  height: 60px;
  line-height: 60px;
  color: #fff;
  cursor: pointer;
  background: rgba(40, 43, 51, 1);
  font-size: 18px;
  font-weight: 400px;
  text-decoration: none;
}
.left-nav > ul a .iconfont {
  display: flex;
  width: 60px;
  height: 60px;
  align-items: center;
  justify-content: center;
  > span {
    display: inline-block;
    width: 14px;
    height: 16px;
    background-image: url(../../assets/images/sprites-nav.png);
    &.icon-home {
      width: 16px;
      height: 14px;
      background-position: -78px -10px;
    }
    &.icon-authentication {
      background-position: -10px -10px;
    }
    &.icon-operation {
      background-position: -114px -10px;
    }
    &.icon-finance {
      background-position: -10px -82px;
    }
    &.icon-monitoring {
      background-position: -44px -46px;
    }
  }
}
.left-nav > ul a .title {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 18px;
  color: #fff;
}
.left-nav > ul a:hover {
  background: rgba(57, 61, 73, 0.7);
}
.left-nav > ul > li a.active {
  background: rgba(57, 61, 73, 1);
  .iconfont > span {
    &.icon-authentication {
      background-position: -44px -10px;
    }
    &.icon-operation {
      background-position: -78px -46px;
    }
    &.icon-finance {
      background-position: -114px -46px;
    }
    &.icon-monitoring {
      background-position: -10px -46px;
    }
  }
}
</style>





