import Axios from 'axios';
const get = (url, params) => {
    // url = '/api/' + url;
    url = '/' + url;
    return new Promise((resolve, reject) => {
        Axios.get(url, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(err => {
            reject(err.data);
        });
    });
};
const post = (url, params, config) => {
    // url = '/api/' + url;
    url = '/' + url;
    return new Promise((resolve, reject) => {
        Axios.post(url, params, config)
        .then(res => {
            resolve(res.data);
        })
        .catch(err => {
            reject(err.data);
        });
    });
};
export default { get, post };


